/* eslint-disable react/jsx-props-no-spreading */
import React from 'react';
import classnames from 'classnames';
import Slider from 'react-slick';
import { Grid, CardMedia, makeStyles } from '@material-ui/core';
import './slider.scss';

const useStyles = makeStyles({
  root: {},
});

const SliderContent = (props) => {
  const { className = '', settings, data, classNameImg, onClickSlider } = props;
  const classes = useStyles();

  const setting = {
    dots: true,
    infinite: true,
    speed: 600,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    arrows: false,
    ...settings,
  };

  const handleOnClick = (obj) => {
    if (onClickSlider && typeof onClickSlider === 'function') {
      onClickSlider(obj);
    }
  };

  if (!data) return null;
  return (
    <Grid item className="slider-content" xs={12}>
      <Slider {...setting}>
        {data &&
          data.map((obj) => (
            <Grid
              key={obj.id}
              className={classnames('', className)}
              onClick={() => handleOnClick(obj)}
              container
              justify="center"
            >
              <Grid item className={classes.container} xs={12}>
                <CardMedia
                  className={classnames('image_show', classNameImg)}
                  component="img"
                  image={obj.imageUrl}
                />
              </Grid>
            </Grid>
          ))}
      </Slider>
    </Grid>
  );
};

export default React.memo(SliderContent);
