/* eslint-disable no-shadow */
import React, { useEffect, useMemo } from 'react';
import { useSelector } from 'react-redux';
import { MODAL_TYPE } from 'src/constants';
import { useModal } from 'src/hooks';
import LoginModal from 'src/modules/auth/components/LoginModal/LoginModal';
import Register from 'src/modules/auth/components/Register/Register';
import ResetPassword from 'src/modules/auth/components/ResetPassword/ResetPassword';
import ModalAddressBooking from 'src/modules/booking/components/addressBooking';
import RegisterComplete from 'src/modules/auth/components/RegisterComplete/RegisterComplete';
import ForceUserLogin from 'src/modules/auth/components/ForceUserLogin/ForceUserLogin';
import LoginSuccess from 'src/modules/auth/components/LoginSuccess/LoginSuccess';
import BookingSuccess from 'src/modules/booking/components/BookingSuccess/BookingSuccess';
import BookingFoods from 'src/modules/order/views/components/requestModal';
import DeliveryAddressModal from 'src/modules/cart/components/DeliveryAddressModal';

import { modalSelector } from 'src/redux/selectors/ui';
import AlertSuccess from 'src/components/base/AlertSuccess/AlertSuccess';
import BranchModal from 'src/components/BranchModal';
import PaymentVoucherSuccessModal from 'src/components/base/layout/paymentSuccessModal';
import { SuccessIcon } from 'src/assets/icons';
import UpdateUserInfo from '../../../modules/auth/components/UpdateUserInfo/UpdateUserInfo';

const ModalUI = () => {
  const modal = useSelector(modalSelector);

  if (typeof modal.type === 'undefined') {
    return <></>;
  }

  return <Modal modal={modal} />;
};

const Modal = ({ modal }) => {
  const { Modal, show, hide } = useModal();

  useEffect(() => {
    if (typeof modal.type !== 'undefined') {
      show();
    }
  }, [modal]);

  const content = useMemo(() => {
    switch (modal.type) {
      case MODAL_TYPE.MODAL_LOGIN:
        return <LoginModal closeModalFn={hide} data={modal.data} />;
      case MODAL_TYPE.MODAL_REGISTER:
        return <Register closeModalFn={hide} data={modal.data} />;
      case MODAL_TYPE.MODAL_RESET_PASSWORD:
        return <ResetPassword closeModalFn={hide} data={modal.data} />;
      case MODAL_TYPE.UPDATE_FIRST_TIME_USER_INFO:
        return <UpdateUserInfo data={modal.data} closeModalFn={hide} />;
      case MODAL_TYPE.REGISTER_ACCOUNT_COMPLETE:
        return <RegisterComplete data={modal.data} closeModalFn={hide} />;
      case MODAL_TYPE.FORCE_USER_LOGIN:
        return <ForceUserLogin data={modal.data} closeModalFn={hide} />;
      case MODAL_TYPE.LOGIN_SUCCESS:
        return <LoginSuccess data={modal.data} closeModalFn={hide} />;
      case MODAL_TYPE.BOOKING_SUCCESS:
        return <BookingSuccess data={modal.data} closeModalFn={hide} />;
      case MODAL_TYPE.MODAL_ADDRESS_BOOKING:
        return (
          <ModalAddressBooking voucher={modal.voucher} closeModalFn={hide} />
        );
      case MODAL_TYPE.MODAL_BOOKING_FOOD:
        return <BookingFoods order={modal.order} closeModalFn={hide} />;
      case MODAL_TYPE.ALERT_SUCCESS:
        return (
          <AlertSuccess
            title={modal.data.title}
            subTitle={modal.data.subTitle}
            titleButton={modal.data.titleButton}
            onClick={hide}
          />
        );
      case MODAL_TYPE.BRANCH_MODAL: {
        return <BranchModal />;
      }
      case MODAL_TYPE.PAYMENT_VOUCHER_SUCCESS:
        return (
          <PaymentVoucherSuccessModal
            icon={<SuccessIcon className="success-icon-payment" />}
            vouchers={modal.data.vouchers}
            tempTotalMoney={modal.data.tempTotalMoneyVoucher}
            discountMoney={modal.data.discountMoneyVoucher}
            shippingFee={modal.data.shippingFee}
            promotionCode={modal.data.promotionCode}
            paymentType={modal.data.paymentType}
            onClick={hide}
          />
        );
      case MODAL_TYPE.DELIVERY_ADDRESS:
        return (
          <DeliveryAddressModal
            icon={<SuccessIcon />}
            // vouchers={modal.data.vouchers}
            // tempTotalMoney={modal.data.tempTotalMoneyVoucher}
            // discountMoney={modal.data.discountMoneyVoucher}
            // promotionCode={modal.data.promotionCode}
            // paymentType={modal.data.paymentType}
            branches={modal.data.branches}
            onClick={hide}
            hideModal={hide}
            onSubmit={modal.data.onSubmit}
          />
        );
      default:
        return <></>;
    }
  }, [modal]);

  return <Modal>{content}</Modal>;
};

export default React.memo(ModalUI);
