/* eslint-disable jsx-a11y/alt-text */
import React from 'react';
import './VoucherItem.scss';
import { EGiftBGIcon, CartIcon, SymbolCurrencyIcon } from 'src/assets/icons';
import { ButtonCustom, CounterInput } from 'src/components/base';
import { Grid } from '@material-ui/core';
import { convertCurrency } from 'src/utils/currency';

const VoucherItem = (props) => {
  const { item } = props;
  const [isSelected, setIsSelected] = React.useState(false);

  return (
    <Grid
      onClick={() => {
        if (typeof props.onItemClick === 'function') {
          props.onItemClick(item);
        }
      }}
      item
      xs={12}
      sm={12}
      md={6}
      lg={4}
      xl={4}
    >
      <div className="voucher-item-container">
        <div className="image-section">
          {item?.imageUrls && item?.imageUrls.length > 0 ? (
            <div className="box-img">
              <img className="thumbnail" src={item.imageUrls[0]} />
            </div>
          ) : (
            <EGiftBGIcon />
          )}
          <div className="content-image-section">
            <img src={item.merchant?.image_url} className="merchant_image" />
            {item.price && item.regular_discount_type === 0 && (
              <div className="percent-item">
                <p className="percent-value">-{item.regular_discount_price}%</p>
              </div>
            )}
            {item.price && item.regular_discount_type === 1 && (
              <div className="percent-item">
                <p className="percent-value">-{item.regular_discount_price}K</p>
              </div>
            )}
          </div>
        </div>
        <div className="content-section">
          <p className="eVoucher-name">{item.name}</p>
          <div className="bottom-section">
            <div className="bottom-left">
              {!!item.price && (
                <p className="price-original">{convertCurrency(item.price)}</p>
              )}
              {!!item.regular_price && (
                <div className="price-section">
                  <SymbolCurrencyIcon />
                  <p className="price">
                    {convertCurrency(item.regular_price)}
                  </p>
                </div>
              )}
            </div>
            {!!item.price && !!item.regular_price && (
              <div className="bottom-right">
                {isSelected ? (
                  <CounterInput />
                ) : (
                  <ButtonCustom
                    title="Chn"
                    icon={<CartIcon className="cart_icon" />}
                    customTitle="customTitle"
                    onClick={() => setIsSelected(true)}
                  />
                )}
              </div>
            )}
          </div>
        </div>
      </div>
    </Grid>
  );
};

export default VoucherItem;
