import { all, fork } from 'redux-saga/effects';
import authSaga from './authSaga';
import dealSaga from './voucher.saga';
import commonSaga from './common.saga';
import merchantSaga from './merchant.saga';
import happyBucketSaga from './happyBucket.saga';
import orderSaga from './order.saga';
import deliverySagas from './delivery.saga';
import promotionSaga from './promotion.saga';
import recruitmentSaga from './recruitment.saga';
import branchSaga from './branch.saga';
import bookingSaga from './booking.saga';
import jobsRecruitmentSaga from './fillRecruitment.saga';
import cooperationSaga from './cooperation.saga';

export default function* rootSaga() {
  yield all([
    fork(authSaga),
    fork(dealSaga),
    fork(commonSaga),
    fork(merchantSaga),
    fork(happyBucketSaga),
    fork(orderSaga),
    fork(deliverySagas),
    fork(promotionSaga),
    fork(promotionSaga),
    fork(recruitmentSaga),
    fork(branchSaga),
    fork(bookingSaga),
    fork(jobsRecruitmentSaga),
    fork(cooperationSaga),
  ]);
}
