import ReactPixel from 'react-facebook-pixel';
import { FACEBOOK_PIXEL_ID } from 'src/constants';

const initReactPixel = (advancedMatching, options) => {
  ReactPixel.init(FACEBOOK_PIXEL_ID, advancedMatching, options);
  ReactPixel.pageView();
};

const trackEvent = (eventName, eventData) => {
  ReactPixel.trackCustom(eventName, eventData);
};

export { initReactPixel, trackEvent };
