import { GET_LIST_MERCHANT } from '../constants/merchant.constant';

export const getListMerchantHandle = (onSuccess) => ({
  type: GET_LIST_MERCHANT.HANDLER,
  onSuccess,
});

export const getListMerchantSuccess = (listMerchant) => ({
  type: GET_LIST_MERCHANT.SUCCESS,
  listMerchant,
});

export const getListMerchantFailure = (error) => ({
  type: GET_LIST_MERCHANT.FAILURE,
  error,
});
