import { REQUEST_BOOKING } from '../constants/booking.constant';

export const requestBookingHandle = (bookingInfo, onSuccess) => ({
  type: REQUEST_BOOKING.HANDLER,
  bookingInfo,
  onSuccess,
});

export const requestBookingSuccess = (userInfo) => ({
  type: REQUEST_BOOKING.SUCCESS,
  userInfo,
});

export const requestBookingFailure = (error) => ({
  type: REQUEST_BOOKING.FAILURE,
  error,
});
