/* eslint-disable react/jsx-one-expression-per-line */
import React from 'react';
import './paymentSuccessModal.scss';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import { convertCurrency } from 'src/utils/currency';
import { PAYMENT_TYPE } from 'src/constants';
import { DishesIcon } from 'src/assets/icons';

const useStyles = makeStyles(() => {
  return {
    'button-custom': {
      background: '#FE5466',
      width: 206,
      height: 48,
      fontFamily: 'Google Sans',
      textTransform: 'none',
    },
  };
});

function PaymentVoucherSuccessModal(props) {
  const classes = useStyles();

  return (
    <div className="payment-cart-success-container">
      <div className="icon-container">{props.icon}</div>
      <p className="title-text">Thanh ton thnh cng</p>
      <div>
        <p className="detail-text">
          Bn va thanh ton thnh cng n hng tr gi &nbsp;
          <span className="point">
            {convertCurrency(props.tempTotalMoney - props.discountMoney)}{' '}
            im.&nbsp;
          </span>
          Chi tit xem ti Lch S Dch V.
        </p>
      </div>
      <div className="content-section">
        <div className="list_voucher_header">
          <DishesIcon />
          <p className="list_voucher_header_title">n hng</p>
        </div>
        <div style={{ marginTop: '20px', marginBottom: '20px' }}>
          {props.vouchers.map((voucher) => (
            <div className="voucher_item">
              <img
                className="voucher_item_image"
                alt=""
                src={voucher.image_urls[0]}
              />
              <p className="voucher_item_quantity">x {voucher.quantity}</p>
              <p className="voucher_item_name">{voucher.name}</p>
              <p className="voucher_item_price">
                {convertCurrency(voucher.regular_price * voucher.quantity)}
              </p>
            </div>
          ))}
        </div>
        <div className="line" />
        <div className="temporary_money_container">
          <p className="temporary_money_title">Tm tnh</p>
          <p className="temporary_money_content">
            {convertCurrency(props.tempTotalMoney)}
          </p>
        </div>
        {!!props.discountMoney && (
          <div className="discount_money_container">
            <p className="promotion_code_title">
              M gim gi({props.promotionCode})
            </p>
            <p className="discount_money_content">
              {convertCurrency(props.discountMoney)}
            </p>
          </div>
        )}
        {!!props.shippingFee && (
          <div className="discount_money_container">
            <p className="promotion_code_title">Ph giao hng</p>
            <p className="shipping_money_content">
              {convertCurrency(props.shippingFee)}
            </p>
          </div>
        )}
        <div className="line" />
        <div className="total_money_container">
          <p className="total_money_title">Tng tin</p>
          <p className="total_money_content">
            {convertCurrency(
              props.tempTotalMoney -
                (props.discountMoney || 0) +
                (props.shippingFee || 0)
            )}
            
          </p>
        </div>
        <div className="payment_method_container">
          <p className="payment_method_title">Hnh thc thanh ton</p>
          <p className="payment_method_content">
            {props.paymentType === PAYMENT_TYPE.POINT ? 'Thanh ton im' : ''}
          </p>
        </div>
      </div>
      <div className="footer-section">
        <Button
          onClick={props.onClick}
          classes={{
            root: classes['button-custom'],
          }}
          variant="contained"
          color="primary"
        >
          Hon tt
        </Button>
      </div>
    </div>
  );
}

export default PaymentVoucherSuccessModal;
