import gql from 'graphql-tag';

export const getListBranchesByIdQuery = gql`
  query list_branches($page: Int, $size: Int, $filter: BranchFilterInput) {
    list_branches(size: $size, page: $page, filter: $filter) {
      data {
        id
        name
        address
        city {
          name
        }
        description
      }
      metadata {
        total
      }
    }
  }
`;

export const getListFoodsMenuQuery = gql`
  query list_foods_menu($page: Int, $size: Int, $filter: FoodMenuFilterInput) {
    list_foods_menu(size: $size, page: $page, filter: $filter) {
      data {
        id
        name
        is_hot
        is_new
        is_drink
        price
        image_url
        created_at
        updated_at
      }
      metadata {
        total
      }
    }
  }
`;
