/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useCallback, useEffect, useRef, useState } from 'react';
import classnames from 'classnames';
import { FormHelperText, Typography } from '@material-ui/core';
import { Title } from 'src/components/base';
import FormBuilder from 'src/components/FormBuilder/FormBuilder';
import { RESEND_CONFIRM_CODE_INTERVAL } from 'src/constants';
import { AvatarIcon } from 'src/assets/icons';
import { Link } from 'react-router-dom';
import './ResetPassword.scss';
import { useDispatch } from 'react-redux';
import {
  checkPhoneNumberHandle,
  resetPasswordHandle,
  verifyTokenFirebaseResetPasswordHandle,
} from 'src/redux/actions/auth.action';
import firebase from 'firebase';
import {
  createAccountFormConfig,
  createAccountValidateSchema,
  fillPhoneFormConfig,
  fillPhoneValidateSchema,
} from './config';
import { AuthFormButton } from '../Button';

const RESET_PASSWORD_STATE_ENUM = {
  FILL_PHONE: 0,
  FILL_CODE: 1,
  RESET_PASSWORD: 2,
};

const CODE_COUNT = 6;

const ResetPassword = (props) => {
  const dispatch = useDispatch();
  const [state, setState] = useState(RESET_PASSWORD_STATE_ENUM.FILL_PHONE);
  const [formInfo, setFormInfo] = useState({});
  const [errorMessage, setErrorMessage] = useState('');
  const [hasSubmit, setHasSubmit] = useState(false);
  const [applicationVerifier, setApplicationVerifier] = useState(null);

  const renderRecapcha = () => {
    setHasSubmit(false);
    const tempApplicationVerifier = new firebase.auth.RecaptchaVerifier(
      'recaptcha-container',
      {
        callback: () => {
          setHasSubmit(true);
        },
      }
    );
    firebase.auth().languageCode = 'vi';
    tempApplicationVerifier.render().then((widgetId) => {
      window.recaptchaWidgetId = widgetId;
    });
    setApplicationVerifier(tempApplicationVerifier);
  };

  useEffect(() => {
    renderRecapcha();
  }, []);

  const handleNextState = () => {
    setState((prev) => {
      return prev + 1;
    });
  };

  const handleRequestOtp = (values) => {
    setErrorMessage('');
    dispatch(
      checkPhoneNumberHandle(values.phone, (res) => {
        if (!res.data.isExist) {
          setErrorMessage('S in thoi khng tn ti');
        } else {
          const tempPhoneNumber = values.phone.slice(1, values.phone.length);
          firebase
            .auth()
            .signInWithPhoneNumber(`+84${tempPhoneNumber}`, applicationVerifier)
            .then((confirmationResult) => {
              setState(RESET_PASSWORD_STATE_ENUM.FILL_CODE);

              setFormInfo({
                ...formInfo,
                phone: values.phone,
                confirmationResult,
              });
            })
            .catch((err) => {
              console.log('error>>>>', err);
            });
        }
      })
    );
  };

  const handleVerifyOtp = async (otp) => {
    setErrorMessage('');
    try {
      await formInfo.confirmationResult.confirm(otp);

      const token = await firebase.auth().currentUser.getIdToken(false);
      dispatch(
        verifyTokenFirebaseResetPasswordHandle(
          formInfo.phone,
          token,
          () => {
            setState(RESET_PASSWORD_STATE_ENUM.RESET_PASSWORD);
          },
          (error) => {
            if (error?.response?.data?.message === 'OTP incorrect') {
              setErrorMessage('M xc thc khng ng');
            }
          }
        )
      );
    } catch (err) {
      switch (err.code) {
        case 'auth/invalid-verification-code': {
          setErrorMessage('M xc thc khng ng');
          break;
        }
        case 'auth/code-expired': {
          setErrorMessage(
            'M xc thc ht hn. Vui lng bm gi li m xc thc'
          );
          break;
        }
        default:
          break;
      }
    }
  };

  const handleCreatePassword = ({ password }) => {
    dispatch(
      resetPasswordHandle({ password }, () => {
        props.closeModalFn();
      })
    );
  };

  const renderState = () => {
    const { FILL_PHONE, FILL_CODE, RESET_PASSWORD } = RESET_PASSWORD_STATE_ENUM;
    switch (state) {
      case FILL_PHONE:
        return (
          <FillPhoneNumberState
            hasSubmit={hasSubmit}
            phone={props?.data?.phone}
            errorMessage={errorMessage}
            onComplete={handleRequestOtp}
          />
        );
      case FILL_CODE:
        return (
          <FillCodeForm
            errorMessage={errorMessage}
            phone={formInfo.phone}
            onComplete={handleVerifyOtp}
            hasSubmit={hasSubmit}
            onUpdateOtpToken={() => {
              setHasSubmit(false);
              const tempApplicationVerifier = new firebase.auth.RecaptchaVerifier(
                'recaptcha-container1',
                {
                  callback: () => {
                    setHasSubmit(true);
                    setErrorMessage('');
                  },
                }
              );
              firebase.auth().languageCode = 'vi';
              // tempApplicationVerifier.render().then((widgetId) => {
              //   window.recaptchaWidgetId = widgetId;
              // });
              const tempPhoneNumber = formInfo.phone.slice(
                1,
                formInfo.phone.length
              );
              firebase
                .auth()
                .signInWithPhoneNumber(
                  `+84${tempPhoneNumber}`,
                  tempApplicationVerifier
                )
                .then((confirmationResult) => {
                  setHasSubmit(true);
                  tempApplicationVerifier.clear();

                  setFormInfo({
                    ...formInfo,
                    phone: formInfo.phone,
                    confirmationResult,
                  });
                })
                .catch((err) => {
                  switch (err.code) {
                    case 'auth/too-many-requests': {
                      setErrorMessage(
                        'S in thoi  b kha do yu cu m xc thc qu nhiu'
                      );
                      break;
                    }

                    default:
                      break;
                  }
                });
            }}
          />
        );
      case RESET_PASSWORD:
        return <CreateAccountForm onComplete={handleCreatePassword} />;
      default:
        return <FillPhoneNumberState onComplete={handleNextState} />;
    }
  };
  return <div className="reset-password-modal">{renderState()}</div>;
};

const FillPhoneNumberState = ({
  phone,
  errorMessage,
  hasSubmit,
  onComplete,
}) => {
  return (
    <div className={classnames('fill-phone-state')}>
      <Typography className="title">t li mt khu</Typography>
      <Typography className="title">
        Vui lng nhp s in thoi ca bn
      </Typography>
      {!!errorMessage && (
        <FormHelperText className={classnames('text-center', 'error')}>
          {errorMessage}
        </FormHelperText>
      )}
      <FormBuilder
        configForm={fillPhoneFormConfig}
        configValidate={fillPhoneValidateSchema}
        initialValues={{
          phone: phone || '',
        }}
        onSubmit={onComplete}
        className="fill-phone-form"
      >
        <div className="recaptcha" id="recaptcha-container" />
        {hasSubmit && (
          <div className="box-button">
            <AuthFormButton title="Tip tc" />
          </div>
        )}
      </FormBuilder>
    </div>
  );
};
const FillCodeForm = ({
  errorMessage: errMessage,
  onComplete,
  onUpdateOtpToken,
  hasSubmit,
}) => {
  const [codes, setCodes] = useState('');
  const [errorMessage, setErrorMessage] = useState('');

  const [startInterval, setStartInverval] = useState(true);
  const [countDown, setCountDown] = useState(RESEND_CONFIRM_CODE_INTERVAL);
  const [elRefs, setElRefs] = React.useState([]);
  const [inputCodeRef, setInputCodeRef] = React.useState(null);

  useEffect(() => {
    setErrorMessage(errMessage);
  }, [errMessage]);

  const interValRef = useRef();

  const onSubmit = useCallback(
    (evt) => {
      evt.preventDefault();
      if (!codes) {
        setErrorMessage('Cha nhp m xc nhn');
        return;
      }
      setErrorMessage('');
      onComplete(codes);
    },
    [codes]
  );

  const handleChange = (evt, index) => {
    evt.preventDefault();
    setCodes((prev) => {
      const newArr = [...prev];
      newArr[index] = evt.target.value;
      return newArr;
    });
    const focusIndex = index < 3 ? index + 1 : index;
    elRefs[focusIndex].current.focus();
  };

  useEffect(() => {
    if (startInterval && !interValRef.current) {
      interValRef.current = setInterval(function () {
        setCountDown((prev) => prev - 1);
      }, 1000);
    } else {
      clearInterval(interValRef.current);
      interValRef.current = false;
    }
    return () => {
      if (interValRef.current) {
        clearInterval(interValRef.current);
      }
    };
  }, [startInterval]);

  useEffect(() => {
    if (countDown <= 0) {
      setStartInverval(false);
    }
  }, [countDown]);

  React.useEffect(() => {
    // add or remove refs
    setElRefs((prevElRefs) =>
      Array(CODE_COUNT)
        .fill()
        .map((_, i) => prevElRefs[i] || React.createRef())
    );
  }, []);
  return (
    <div className={classnames('fill-code-form')}>
      <Typography className="title">Nhp m xc thc</Typography>
      <div className="box-subtitle">
        <Typography className="subtitle">
          Vui lng nhp m xc thc c gi n s in thoi ca bn
        </Typography>
      </div>
      <form onSubmit={onSubmit} className="form-fill-code">
        <input
          className="input_code"
          maxLength={CODE_COUNT}
          pattern="^[0-9]+$"
          onChange={(evt) => {
            if (evt.target.value === '' || /^[0-9]+$/.test(evt.target.value)) {
              setErrorMessage('');
              setCodes(evt.target.value);
            }
          }}
          value={codes}
          ref={(input) => {
            setInputCodeRef(input);
          }}
        />
        <ul className="input-list">
          {Array(CODE_COUNT)
            .fill('')
            .map((item, index) => (
              <li key={`input.item.${index}`} className="input-item">
                <div
                  onClick={() => {
                    inputCodeRef.focus();
                  }}
                  type="text"
                  className="display-code"
                  onChange={(evt) => handleChange(evt, index)}
                  ref={elRefs[index]}
                >
                  {codes[index] || ''}
                </div>
              </li>
            ))}
        </ul>
        {!!errorMessage && (
          <FormHelperText className={classnames('text-center', 'error')}>
            {errorMessage}
          </FormHelperText>
        )}
        {hasSubmit && (
          <div className="box-button">
            <AuthFormButton title="Tip tc" />
          </div>
        )}

        {startInterval ? (
          <FormHelperText
            className={classnames('text-center', 'resend_inactive')}
          >
            Gi li m xc thc ({countDown}s)
          </FormHelperText>
        ) : (
          hasSubmit && (
            <FormHelperText
              onClick={() => {
                setCodes('');
                setErrorMessage('');
                onUpdateOtpToken();
                setStartInverval(true);
                setCountDown(RESEND_CONFIRM_CODE_INTERVAL);
              }}
              className={classnames('text-center', 'resend_active')}
            >
              Bm  gi li m xc thc
            </FormHelperText>
          )
        )}
      </form>
    </div>
  );
};

const CreateAccountForm = ({ onComplete }) => {
  const onSubmit = useCallback((values) => {
    console.log('values | ', values);
    onComplete(values);
  }, []);
  return (
    <div className="create-account-form">
      <AvatarIcon className="ic-default-avatar" />
      <Title>t mt khu</Title>
      <FormBuilder
        onSubmit={onSubmit}
        configForm={createAccountFormConfig}
        configValidate={createAccountValidateSchema}
        initialValues={{
          password: '',
          confirm: '',
        }}
      >
        <div className="box-button">
          <AuthFormButton title="Tip tc" />
        </div>
        <FormHelperText className="description">
          Qua vic ng nhp hoc to ti khon, bn ng  vi cc{' '}
          <Link>iu khon v iu kin </Link> cng nh&nbsp;
          <Link>Chnh sch An ton v Bo mt</Link> ca chng ti
        </FormHelperText>
      </FormBuilder>
    </div>
  );
};

export default ResetPassword;
