/* eslint-disable no-return-await */
import {
  getListBannerQuery,
  getSettingByNameQuery,
  getListNotificationsQuery,
  readNotificationQuery,
} from 'src/graphql/queries/common.query';
import APIUtils from 'src/utils/apiUtils';
import { API_URL } from 'src/constants';
import apolloClient from '../graphql/index';

export function getListBannerApi() {
  return apolloClient
    .query({
      query: getListBannerQuery,

      context: {
        service: 'common',
      },
    })
    .then((res) => res.data);
}

export function getSettingByNameApi(name) {
  return apolloClient
    .query({
      query: getSettingByNameQuery,
      variables: {
        name,
      },
      context: {
        service: 'common',
      },
    })
    .then((res) => res.data);
}

export function getListNotificationsApi(page, size, filter, sorts) {
  return apolloClient
    .query({
      query: getListNotificationsQuery,
      variables: {
        page,
        size,
        filter,
        sorts,
      },
      context: {
        service: 'common',
      },
    })
    .then((res) => res.data);
}

export function readNotificationApi(id) {
  return apolloClient
    .query({
      query: readNotificationQuery,
      variables: {
        id,
      },
      context: {
        service: 'common',
      },
    })
    .then((res) => res.data);
}

export const sendFCMTokenToReceiveNotifications = async (dataFCM) => {
  return await APIUtils.post(`${API_URL}/firebase`, dataFCM, {
    'Content-Type': 'application/json',
  });
};
