import React from 'react';
import './confirmModalVoucher.scss';
import { ButtonCustom } from 'src/components/base';
import PropTypes from 'prop-types';

import { SuccessCircleIcon, FailureCircleIcon } from 'src/assets/icons';

const ConfirmModalVoucher = ({
  isSuccess = true,
  onClickModal = () => {},
  title = 'S dng Voucher thnh cng',
  des = 'Bn va s dng thnh cng E-Voucher My Home 300.000',
  buttonTitle = 'Hon tt',
}) => {
  const icon = isSuccess ? <SuccessCircleIcon /> : <FailureCircleIcon />;
  return (
    <div className="confirm-modal-container">
      {icon}
      <p className={`title-confirm  ${!isSuccess && 'delete'}`}>{title}</p>
      <span className="des-confirm">{des}</span>
      <ButtonCustom
        item
        title={buttonTitle}
        customStyle="button-apply"
        customTitle="button-apply-title"
        onClick={onClickModal}
      />
    </div>
  );
};

ConfirmModalVoucher.propTypes = {
  isSuccess: PropTypes.bool.isRequired,
  onClickModal: PropTypes.func.isRequired,
  title: PropTypes.string.isRequired,
  des: PropTypes.string.isRequired,
  buttonTitle: PropTypes.string.isRequired,
};

export default ConfirmModalVoucher;
