/* eslint-disable camelcase */
import gql from 'graphql-tag';

export const getDealByIdQuery = gql`
  query get_deal($id: Int!) {
    get_deal(id: $id) {
      id
      merchant_id
      merchant {
        id
        name
        image_url
        description
      }
      branches {
        id
        name
        lat
        lng
        address
        image_url
      }
      category {
        id
        name
      }
      name
      description
      condition
      search_text
      order
      rate
      image_urls
      booking_type
      min_booking_time
      enable_voucher
      booking_type
      hot
      feature
      start_at
      expired_at
      quantity
      price
      regular_price
      regular_discount_price
      regular_discount_type
      is_sale
      sale_price
      sale_discount_price
      sale_discount_type
      created_at
      updated_at
    }
  }
`;

export const getListDealQuery = gql`
  query list_deals(
    $page: Int
    $size: Int
    $filter: DealFilterInput
    $sorts: [SortInput]
  ) {
    list_deals(size: $size, page: $page, filter: $filter, sorts: $sorts) {
      data {
        id
        image_urls
        name
        regular_price
        regular_discount_type
        regular_discount_price
        price
        merchant {
          image_url
        }
      }
      metadata {
        total
      }
    }
  }
`;

export const getListDeliveryFoodsQuery = gql`
  query list_delivery_foods($size: Int, $page: Int) {
    list_delivery_foods(
      sorts: [{ field: "created_at", order: "desc" }]
      size: $size
      page: $page
    ) {
      data {
        id
        user {
          name
        }
        address
        code
        total_price
        deals {
          deal {
            id
            regular_price
            name
            image_urls
          }
          quantity
        }
        branch {
          name
          merchant {
            name
            image_url
          }
        }
        shipping_distance
        shipping_price
        shipping_date
        shipping_time
        created_at
        note
        promotion_code
        total_price
        total_price_discount
        delivery_status
        purchase_status
      }
      metadata {
        total
      }
    }
  }
`;

