import { put, takeLatest } from 'redux-saga/effects';

import {
  getListBannerApi,
  getSettingByNameApi,
  getListNotificationsApi,
  readNotificationApi,
} from 'src/services/common.service';
import {
  GET_BANK_INFO,
  GET_LIST_BANNER,
  GET_LIST_FAQ,
  GET_LIST_NOTIFICATIONS,
  GET_READ_NOTIFICATION,
} from '../constants/common.constant';
import {
  getBankInfoFailure,
  getBankInfoSuccess,
  getListBannerFailure,
  getListBannerSuccess,
  getListNotificationsSuccess,
  getListNotificationsFailure,
  getReadNotificationSuccess,
  getReadNotificationFailure,
  getListFAQSuccess,
  getListFAQFailure,
} from '../actions/common.action';
import {
  listBannerParse,
  listNotificationsParse,
  notificationDetailParse,
} from '../parser/common.parser';

export function* getListBannerSaga(action) {
  const { onSuccess } = action;
  try {
    const res = yield getListBannerApi();
    const banners = listBannerParse(res.list_banners.data);
    yield put(getListBannerSuccess(banners));
    if (typeof onSuccess === 'function') {
      onSuccess(banners);
    }
  } catch (error) {
    yield put(getListBannerFailure(error.message));
  }
}

export function* getListFAQSaga(action) {
  const { onSuccess } = action;
  try {
    const res = yield getSettingByNameApi('faq_section');
    const listQuestion = (JSON.parse(res.get_setting_by_name?.data) || {}).faq;
    yield put(getListFAQSuccess(listQuestion));
    if (typeof onSuccess === 'function') {
      onSuccess(listQuestion);
    }
  } catch (error) {
    yield put(getListFAQFailure(error.message));
  }
}

export function* getBankInfoSaga(action) {
  const { onSuccess } = action;
  try {
    const res = yield getSettingByNameApi('bankinfo');
    yield put(getBankInfoSuccess(res.get_setting_by_name));
    if (typeof onSuccess === 'function') {
      onSuccess(res.get_setting_by_name);
    }
  } catch (error) {
    yield put(getBankInfoFailure(error.message));
  }
}

export function* getListNotificationsSaga(action) {
  const { page = 1, pageSize = 6, filter, sorts } = action;
  try {
    const res = yield getListNotificationsApi(page, pageSize, filter, sorts);
    const notifications = listNotificationsParse(res?.list_notifications?.data);
    yield put(
      getListNotificationsSuccess({
        data: notifications,
        page,
        pageSize,
        total: res?.list_notifications?.metadata?.total,
      })
    );
  } catch (error) {
    yield put(getListNotificationsFailure(error));
  }
}

export function* readNotificationSaga(action) {
  const { id, onSuccess } = action;
  try {
    const res = yield readNotificationApi(id);
    const notification = notificationDetailParse(res?.read_notification);
    yield put(getReadNotificationSuccess(notification));
    if (typeof onSuccess === 'function') {
      onSuccess(notification);
    }
  } catch (error) {
    yield put(getReadNotificationFailure(error));
  }
}

export default function* dealSaga() {
  yield takeLatest(GET_LIST_BANNER.HANDLER, getListBannerSaga);
  yield takeLatest(GET_BANK_INFO.HANDLER, getBankInfoSaga);
  yield takeLatest(GET_LIST_NOTIFICATIONS.HANDLER, getListNotificationsSaga);
  yield takeLatest(GET_READ_NOTIFICATION.HANDLER, readNotificationSaga);
  yield takeLatest(GET_LIST_FAQ.HANDLER, getListFAQSaga);
}
