import { put, takeLatest } from 'redux-saga/effects';

import { getListNewsApi } from 'src/services/news.service';

import { getDealByIdApi } from 'src/services/deal.service';
import { GET_HAPPY_BUCKET_BY_ID } from '../constants/happyBucket.constant';
import {
  getHappyBucketByIdFailure,
  getHappyBucketByIdSuccess,
} from '../actions/happyBucket.action';
import {
  GET_LIST_PROMOTION,
  GET_LIST_PROMOTION_HOME,
} from '../constants/promotion.constant';
import { listNewsParse } from '../parser/news.parser';
import {
  getListPromotionFailure,
  getListPromotionHomeFailure,
  getListPromotionHomeSuccess,
  getListPromotionSuccess,
} from '../actions/promotion.action';

export function* getHappyBucketByIdSaga(action) {
  const { onSuccess, happyBucketId } = action;
  try {
    const res = yield getDealByIdApi(happyBucketId);
    yield put(getHappyBucketByIdSuccess(res));
    if (typeof onSuccess === 'function') {
      onSuccess(res.get_deal);
    }
  } catch (error) {
    yield put(getHappyBucketByIdFailure(error.message));
  }
}

export function* getListPromotionHomeSaga(action) {
  const { onSuccess, page = 1, size = 3 } = action;
  try {
    const res = yield getListNewsApi(page, size, { news_type: 0 });
    const data = listNewsParse(res?.list_news?.data);

    yield put(getListPromotionHomeSuccess(data));
    if (typeof onSuccess === 'function') {
      onSuccess(data);
    }
  } catch (error) {
    yield put(getListPromotionHomeFailure(error.message));
  }
}

export function* getListPromotionSaga(action) {
  const { onSuccess, page = 1, size = 10 } = action;
  try {
    const res = yield getListNewsApi(page, size, { news_type: 0 });
    const data = listNewsParse(res?.list_news?.data);
    yield put(getListPromotionSuccess({ ...res?.list_news, data }));
    if (typeof onSuccess === 'function') {
      onSuccess({ ...res?.list_news, data });
    }
  } catch (error) {
    yield put(getListPromotionFailure(error.message));
  }
}

export default function* promotionSaga() {
  yield takeLatest(GET_LIST_PROMOTION_HOME.HANDLER, getListPromotionHomeSaga);
  yield takeLatest(GET_LIST_PROMOTION.HANDLER, getListPromotionSaga);
  yield takeLatest(GET_HAPPY_BUCKET_BY_ID.HANDLER, getHappyBucketByIdSaga);
}
