import ReactGA from 'react-ga';
import { TRACKING_ID } from 'src/constants';

const initGoogleAnalytics = () => {
  ReactGA.initialize(TRACKING_ID, {});
};

const trackingEvent = (trackData) => {
  ReactGA.event(trackData);
};

const trackingPageView = (path) => {
  ReactGA.pageview(path);
};

export { trackingEvent, trackingPageView, initGoogleAnalytics };
