/* eslint-disable import/prefer-default-export */
function toRad(Value) {
  return (Value * Math.PI) / 180;
}

export const calculateDistance = (fromLocation, toLocation) => {
  const R = 6371; // km
  const dLat = toRad(toLocation.lat - fromLocation.lat);
  const dLon = toRad(toLocation.lng - fromLocation.lng);
  const lat1 = toRad(fromLocation.lat);
  const lat2 = toRad(toLocation.lat);

  const a =
    Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.sin(dLon / 2) * Math.sin(dLon / 2) * Math.cos(lat1) * Math.cos(lat2);
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  const d = R * c;
  return d;
};

