import {
  GET_LIST_VOUCHER_HISTORY,
  GET_VOUCHERS_BY_SEARCH,
  SET_KEYWORD_FOR_SEARCH,
} from '../constants/voucher.constant';

const initialState = {
  listVouchersHistory: [],
  suggestions: [],
  totalSearch: 0,
  keyword: '',
};

export default function voucherReducer(state = initialState, action) {
  const { type } = action;
  switch (type) {
    case GET_LIST_VOUCHER_HISTORY.SUCCESS: {
      return {
        ...state,
        listVouchersHistory: action.listVoucher,
      };
    }

    case GET_VOUCHERS_BY_SEARCH.SUCCESS: {
      const { suggestions, total } = action.payload;
      const newList = state.suggestions.slice();
      newList.push(...suggestions);
      return {
        ...state,
        suggestions: newList,
        totalSearch: total,
      };
    }

    case SET_KEYWORD_FOR_SEARCH.HANDLER: {
      return {
        ...state,
        keyword: action.payload,
      };
    }

    default:
      return state;
  }
}
