import { takeLatest, call, put } from 'redux-saga/effects';
import { requestCooperationInfoApi } from 'src/services/recruitment.service';
import {
  requestCooperationRecruitmentFailure,
  requestCooperationRecruitmentSuccess,
} from '../actions/cooperation.action';

import { POST_COOPERATION_INFO } from '../constants/cooperation.constant';

export function* requestCooperationRecruitmentSaga({ params, onSuccess }) {
  try {
    const res = yield call(requestCooperationInfoApi, params);
    if (typeof onSuccess === 'function') {
      onSuccess(res);
    }
    yield put(requestCooperationRecruitmentSuccess(res));
  } catch (err) {
    yield put(requestCooperationRecruitmentFailure(err));
  }
}

export default function* cooperationSaga() {
  yield takeLatest(
    POST_COOPERATION_INFO.HANDLER,
    requestCooperationRecruitmentSaga
  );
}
