import firebase from 'firebase';
import 'firebase/messaging';
import { firebaseCloudMessagingConfig } from 'src/constants';

const askForPushNotificationPermission = (onGranted, onDenied) => {
  if (firebase.messaging.isSupported()) {
    const messaging = firebase.messaging();
    messaging
      .getToken({
        vapidKey: firebaseCloudMessagingConfig.VALID_KEY_FCM,
      })
      .then((currentToken) => {
        if (currentToken) {
          onGranted(currentToken);
          console.log('TOKEN FCM>>>>>', currentToken);
        } else {
          onDenied();
          console.log('Ask for permission');
        }
      })
      .catch((error) => {
        console.log('ERROR FCM', error);
      });
  }
};

const onMessageReceived = (onReceived) => {
  if (firebase.messaging.isSupported()) {
    const messaging = firebase.messaging();

    messaging.onMessage(
      (messageReceived) => {
        console.log('RECEIVED A MESSAGE', messageReceived);
        onReceived(messageReceived);
      },
      (error) => {
        console.log('ERROR WHEN RECEIVED A MESSAGE', error);
      }
    );
  }
};

const onBackgroundReceivedMessage = () => {
  if (firebase.messaging.isSupported()) {
    const messaging = firebase.messaging();

    messaging.onBackgroundMessage((a) => {
      console.log('123123123123123123123123', a);
    });
  }
};

export {
  askForPushNotificationPermission,
  onMessageReceived,
  onBackgroundReceivedMessage,
};
