import * as Yup from 'yup';

export const fillPhoneFormConfig = [
  {
    name: 'phone',
    key: 'phone',
    type: 'icon-input',
    required: true,
    props: {
      placeholder: 'S in thoi',
      icon: 'phone',
      className: 'phone-input',
      inputProps: { maxLength: 10 },
    },
  },
];

export const fillPhoneValidateSchema = Yup.object().shape({
  phone: Yup.string()
    .required('Vui lng nhp s in thoi')
    .matches(
      /(03|05|07|08|09|01[2|6|8|9])+([0-9]{8})\b/,
      'S in thoi khng hp l'
    ),
});

