  getListFeatureVoucherHomeFailure,
  getListVoucherByTypeSuccess,
  getListVoucherByTypeFailure,
  getListVoucherByMerchantSuccess,
  getListVoucherByMerchantFailure,
  getListVoucherHistoryFailure,
  getCodeRequiredHistorySuccess,
  getCodeRequiredHistoryFailure,
  deleteVoucherItemHistoryFailure,
  getListVoucherTypeSuccess,
