export function* getVoucherByIdSaga(action) {
  const { onSuccess, voucherId } = action;
  try {
    const res = yield getDealByIdApi(parseInt(voucherId));
    const voucher = voucherDetailsParse(res.get_deal);
    yield put(getVoucherByIdSuccess(voucher));
    if (typeof onSuccess === 'function') {
      onSuccess(voucher);
    }
  } catch (error) {
    yield put(getVoucherByIdFailure(error.message));
  }
}

export function* getListHotVoucherHomeSaga(action) {
  const { onSuccess, page = 1, size = 6 } = action;
  try {
    const res = yield getListDealApi(page, size, { hot: true }, [
      { field: 'order', order: 'desc' },
      { field: 'created_at', order: 'desc' },
    ]);
    const data = listVoucherParse(res?.list_deals?.data);
    yield put(getListHotVoucherHomeSuccess(data));
    if (typeof onSuccess === 'function') {
      onSuccess(data);
    }
  } catch (error) {
    yield put(getListHotVoucherHomeFailure(error.message));
  }
}

