// Delete voucher history

export function* deleteVoucherHistorySaga(action) {
  const { voucherId, onSuccess } = action;
  try {
    const res = yield deleteVoucherHistoryApi(voucherId);
    const data = res?.mark_voucher_from_user;
    // yield put(deleteVoucherItemHistorySuccess(data));
    if (typeof onSuccess === 'function') {
      onSuccess(data);
    }
  } catch (error) {
    yield put(deleteVoucherItemHistoryFailure(error.message));
  }
}

export function* useVoucherHistorySaga(action) {
  const { voucherId, agent_code, onSuccess, onError } = action;
  try {
    const res = yield useVoucherHistoryApi({ voucherId, agent_code });
    const data = res?.use_voucher;
    // yield put(deleteVoucherItemHistorySuccess(data));
    if (typeof onSuccess === 'function') {
      onSuccess(data);
    }
  } catch (error) {
    if (typeof onError === 'function') {
      onError(error);
    }
    yield put(deleteVoucherItemHistoryFailure(error.message));
  }
}

export function* buyVoucherSaga(action) {
  const { listDeal, promotionCode, onSuccess, onError } = action;
  try {
    const res = yield getVouchersForListDealApi({
      deals: listDeal,
      promotion_code: promotionCode,
    });
    if (!res.errors) {
      yield put(buyVoucherSuccess(res));

      if (typeof onSuccess === 'function') {
        onSuccess(res);
      }
    } else {
      throw res.errors;
    }
  } catch (error) {
    yield put(buyVoucherFailure(error));
    if (typeof onError === 'function') {
      onError(
        Array.isArray(error) && error.length > 0
          ? error[0]
          : { message: 'C li xy ra' }
      );
    }
  }
}

function* checkPromotionCodeSaga(action) {
  const { code, totalPrice, onSuccess, onError } = action;
  console.log('deleteVoucherHistorySaga: ', action);
  try {
    let deviceToken = yield select(getDeviceTokenSelector);
    if (!deviceToken) {
      deviceToken = yield getDeviceTokenSaga();
    }
    const res = yield checkPromotionCodeApi(
      { code, totalPrice },
      { Authorization: 'Bearer ' }
    );
    console.log('res: ', res);
    yield put(checkPromotionCodeSuccess(res));
    if (typeof onSuccess === 'function') {
      onSuccess(res);
    }
  } catch (error) {
    yield put(checkPromotionCodeFailure(error.message));
    if (typeof onError === 'function') {
      onError(error);
    }
  }
}

export default function* dealSaga() {
  yield takeLatest(
    GET_LIST_HOT_VOUCHER_HOME.HANDLER,
    getListHotVoucherHomeSaga
  );
  yield takeLatest(
    GET_LIST_FEATURE_VOUCHER_HOME.HANDLER,
    getListFeatureVoucherHomeSaga
  );
  yield takeEvery(GET_LIST_VOUCHER_BY_TYPE.HANDLER, getListVoucherByTypeSaga);
  yield takeEvery(GET_LIST_VOUCHER_TYPE.HANDLER, getListVoucherTypeSaga);
  yield takeEvery(
    GET_LIST_VOUCHER_BY_MERCHANT.HANDLER,
    getListVoucherByMerchantIdSaga
  );
  yield takeLatest(GET_VOUCHER_BY_ID.HANDLER, getVoucherByIdSaga);
  // Voucher history
  yield takeEvery(GET_LIST_VOUCHER_HISTORY.HANDLER, getListVoucherHistorySaga);
  // Voucher code required
  yield takeEvery(
    GET_LIST_CODE_REQUIRED_VOUCHER_HISTORY.HANDLER,
    getListCodeRequiredSaga
  );
  yield takeEvery(DELETE_VOUCHER_HISTORY.HANDLER, deleteVoucherHistorySaga);
  yield takeEvery(USE_VOUCHER_HISTORY.HANDLER, useVoucherHistorySaga);
  yield takeEvery(DELETE_VOUCHER_HISTORY.HANDLER, deleteVoucherHistorySaga);
  yield takeEvery(CHECK_PROMOTION_CODE.HANDLER, checkPromotionCodeSaga);
  yield takeEvery(BUY_VOUCHER.HANDLER, buyVoucherSaga);
  yield takeEvery(GET_VOUCHERS_BY_SEARCH.HANDLER, getVouchersBySearchSaga);
}
