import React, { useEffect } from 'react';
import './CounterInput.scss';

const CounterInput = (props) => {
  const { handleDecrement, handleIncrement, customStyle, value } = props;
  const [counter, setCounter] = React.useState(value);

  useEffect(() => {
    if (value) {
      setCounter(value);
    }
  }, [value]);

  return (
    <div className={`counter_input_container ${customStyle}`}>
      <button type="button" className="button" onClick={handleDecrement}>
        -
      </button>
      <p className="value">{counter}</p>
      <button type="button" className="button" onClick={handleIncrement}>
        +
      </button>
    </div>
  );
};

export default CounterInput;
