/* eslint-disable import/prefer-default-export */
import gql from 'graphql-tag';

export const getListNewsQuery = gql`
  query list_news($page: Int, $size: Int, $filter: NewsFilterInput) {
    list_news(size: $size, page: $page, filter: $filter) {
      data {
        id
        name
        description
        cover_img
        created_at
        data
      }
      metadata {
        total
      }
    }
  }
`;
