/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { Box, Link } from '@material-ui/core';
import { AvatarIcon } from 'src/assets/icons';
import { Title } from 'src/components/base';
import FormBuilder from 'src/components/FormBuilder/FormBuilder';
import './LoginModal.scss';
import { loginHandle } from 'src/redux/actions/auth.action';
import { useDispatch } from 'react-redux';
import { MODAL_TYPE } from 'src/constants';
import { addModal } from 'src/redux/actions/ui.action';
import { AuthFormButton } from '../Button/BaseButton';
import { loginValidateSchema, normalLoginConfig } from './config';

const LoginModal = React.memo(({ data, closeModalFn }) => {
  const dispatch = useDispatch();
  const [errorMessage, setErrorMessage] = useState('');
  return (
    <div className="login-modal">
      <AvatarIcon className="ic-default-avatar" />
      <Title className="login_title">ng nhp</Title>
      {!!errorMessage && (
        <p className="login_error">Ti khon hoc mt khu khng ng</p>
      )}

      <FormBuilder
        configForm={normalLoginConfig}
        configValidate={loginValidateSchema}
        initialValues={{
          phone: data?.phone || '',
          password: '',
        }}
        onSubmit={(values) => {
          setErrorMessage('');
          dispatch(
            loginHandle(
              values,
              (res) => {
                if (res.profile_need_update) {
                  dispatch(
                    addModal({
                      type: MODAL_TYPE.UPDATE_FIRST_TIME_USER_INFO,
                      data: values,
                    })
                  );
                } else if (typeof closeModalFn === 'function') {
                  dispatch(
                    addModal({
                      type: MODAL_TYPE.LOGIN_SUCCESS,
                      data: values,
                    })
                  );
                }
              },
              (error) => {
                console.log('error', error?.response?.data?.message);

                if (error?.response?.data?.message === 'incorrect password') {
                  setErrorMessage('Ti khon hoc mt khu khng ng');
                }
                if (
                  error?.response?.data?.message ===
                  'cannot get user with phone'
                ) {
                  dispatch(
                    addModal({
                      type: MODAL_TYPE.MODAL_REGISTER,
                      data: { ...values },
                    })
                  );
                }
              }
            )
          );
        }}
      >
        <Box display="flex" justifyContent="flex-end" style={{ width: '100%' }}>
          <Link
            onClick={() => {
              dispatch(
                addModal({
                  type: MODAL_TYPE.MODAL_RESET_PASSWORD,
                })
              );
            }}
            className="forgot-password"
          >
            Qun mt khu
          </Link>
        </Box>
        <p className="mar-b-20">
          ng nhp ngay  s dng nhng dch v tuyt vi t My Home
        </p>
        <AuthFormButton title="ng nhp" />
      </FormBuilder>
      <Link className="mar-b-15" onClick={closeModalFn}>
        B qua
      </Link>
      <div style={{ display: 'flex', alignItems: 'center' }}>
        <div className="login_form_register_title">Bn cha c ti khon? </div>
        <div
          className="login_form_register_button"
          onClick={() => {
            dispatch(
              addModal({
                type: MODAL_TYPE.MODAL_REGISTER,
              })
            );
          }}
        >
          &nbsp; ng k
        </div>
      </div>
    </div>
  );
});

LoginModal.propTypes = {
  closeModalFn: PropTypes.func.isRequired,
};

export default LoginModal;
