/* eslint-disable import/prefer-default-export */
export const listHappyBucketParse = (listHappyBucket) => {
  if (listHappyBucket && listHappyBucket.length > 0) {
    return listHappyBucket.map((happyBucket) => {
      return {
        ...happyBucket,
        id: happyBucket.id,
        imageUrls: happyBucket.image_urls,
        name: happyBucket.name,
        description: happyBucket.description,
      };
    });
  }
  return [];
};
