import React from 'react';
import './OrderDetail.scss';
import { CounterInput, ButtonCustom } from 'src/components/base';
import { CartIconWhite, SymbolCurrencyIcon } from 'src/assets/icons';
import { Grid } from '@material-ui/core';
import { useSelector } from 'react-redux';
import { getListSelectedSelector } from 'src/redux/selectors/order.selector';
import { convertCurrency } from 'src/utils/currency';

const OrderDetail = (props) => {
  const { item, branch, onAddItemClick, updateListSelectedHandle } = props;
  const listSelected = useSelector(getListSelectedSelector);

  const currentSelected = listSelected.find((el) => el.id === item.id);

  const getQuantity = (quantity) => {
    updateListSelectedHandle({ id: item.id, quantity });
  };

  return (
    <Grid item xl={4} lg={4} md={6} sm={6} xs={12}>
      <div className="order-detail-item-container">
        <div className="image-section">
          <div className="box-img">
            <img
              src={branch.image_url}
              style={{ objectFit: 'cover' }}
              alt="order-detail"
            />
            <img
              src={branch?.merchant?.image_url}
              alt="logo"
              className="logo"
            />
          </div>
        </div>

        <div className="content-section">
          <p className="eVoucher-name">{item.name}</p>
          <div className="bottom-section">
            <div className="bottom-left">
              {!!item.price && (
                <div className="price-section">
                  <SymbolCurrencyIcon />
                  <p className="price">{convertCurrency(item.price)}</p>
                </div>
              )}
            </div>
            <div className="bottom-right">
              {currentSelected?.quantity ? (
                <CounterInput
                  handleDecrement={() => {
                    if (currentSelected?.quantity > 0) {
                      getQuantity(currentSelected?.quantity - 1);
                    }
                  }}
                  handleIncrement={() => {
                    getQuantity(currentSelected?.quantity + 1);
                  }}
                  value={currentSelected?.quantity || null}
                />
              ) : (
                <ButtonCustom
                  title="Chn"
                  icon={<CartIconWhite />}
                  customTitle="customTitle"
                  onClick={() => {
                    onAddItemClick({ item });
                  }}
                />
              )}
            </div>
          </div>
        </div>
      </div>
    </Grid>
  );
};

export default OrderDetail;
