export const listBranchesParse = (branches) => {
  if (branches && branches.length > 0) {
    return branches.map((el) => {
      return {
        id: el.id,
        name: el.name,
        address: el.address,
        description: el.description,
      };
    });
  }
  return [];
};

export const listFoodsMenuParse = (listMenu) => {
  if (listMenu && listMenu.length > 0) {
    return listMenu.map((el) => {
      return {
        id: el.id,
        name: el.name,
        is_hot: el.is_hot,
        is_new: el.is_new,
        is_drink: el.is_drink,
        price: el.price,
        address: el.address,
        image_url: el.image_url,
        description: el.description,
      };
    });
  }
  return [];
};
