import React from 'react';
import { StarIcon, StarIconActive } from 'src/assets/icons';

const array = [
  { index: 0 },
  { index: 1 },
  { index: 2 },
  { index: 3 },
  { index: 4 },
];

const star = 3;

function StarEvaluate() {
  return (
    <div className="star-container">
      {array.map((obj) => {
        if (obj.index < star) {
          return <StarIconActive />;
        }
        return <StarIcon />;
      })}
    </div>
  );
}

export default StarEvaluate;
