import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';

// language
import vn from './lang/vn.json';
import us from './lang/us.json';

i18n.use(initReactI18next).init({
  resources: {
    vn,
    us,
  },
  lng: 'vn',
  fallbackLng: 'vn',
  interpolation: {
    escapeValue: false,
  },
});

export default i18n;
