/* eslint-disable import/prefer-default-export */
/* eslint-disable no-return-await */
import { getListBranchesQuery } from 'src/graphql/queries/branch.query';
import apolloClient from '../graphql/index';

export function getListBranchesApi(page, size, filter) {
  return apolloClient
    .query({
      query: getListBranchesQuery,
      variables: {
        page,
        size,
        filter,
      },
      context: {
        service: 'order',
      },
    })
    .then((res) => res.data);
}
