/* eslint-disable no-unused-vars */
import React, { useEffect, useState } from 'react';
import './BranchModal.scss';
import { Map, InfoWindow, Marker, GoogleApiWrapper } from 'google-maps-react';
import { GOOGLE_MAP_API_KEY } from 'src/constants';
import { Grid, Modal as BaseModal } from '@material-ui/core';
import classNames from 'classnames';
import { useSelector } from 'react-redux';
import { getCurrentLocationSelector } from 'src/redux/selectors/common.selector';
import { IconButton } from 'src/components/base';
import { formatDistance } from 'src/utils/formatString';
import { calculateDistance } from 'src/utils/distance';
import MarkerIcon from '../../assets/icons/icon-marker.svg';

const BranchModal = (props) => {
  const listBranch = props.listBranch || [];
  const [selectedBranch, setSelectedBranch] = useState(
    listBranch.length > 0 ? listBranch[0] : {}
  );
  const [activeMarker, setActiveMarker] = useState({});
  const [activeBranchMarker, setActiveBranchMarker] = useState({});
  const currentLocation = useSelector(getCurrentLocationSelector);
  useEffect(() => {
    if (props.listBranch && props.listBranch.length > 0) {
      setSelectedBranch(props.listBranch[0]);
    }
  }, [props.listBranch]);

  // eslint-disable-next-line no-shadow
  const onMarkerClick = (props, marker) => {
    setActiveBranchMarker(props.position);
    setActiveMarker(marker);
  };

  // console.log(
  //   'selectedBranch',
  //   calculateDistance(
  //     {
  //       lat: currentLocation.latitude,
  //       lng: currentLocation.longitude,
  //     },
  //     listBranch[0]
  //   )
  // );
  return (
    <BaseModal className="list_branch_base_modal" open={props.open}>
      <div className="branch_container">
        <Grid container>
          <Grid item xl={4} lg={4} md={4} sm={12} xs={12}>
            <div className="list_branch_container">
              <div className="title_container">
                <div className="title">a ch p dng</div>
                <div className="count_branch">{`${listBranch.length} a ch`}</div>
              </div>
              <div className="list_branch">
                {listBranch.map((obj) => (
                  <div
                    onClick={() => {
                      setSelectedBranch(obj);
                    }}
                  >
                    <div
                      className={classNames(
                        'branch_container',
                        selectedBranch.id === obj.id
                          ? 'branch_container_active'
                          : 'branch_container_inactive'
                      )}
                    >
                      <div
                        className={classNames(
                          'branch_address',
                          selectedBranch.id === obj.id
                            ? 'branch_address_active'
                            : 'branch_address_inactive'
                        )}
                      >
                        {obj.address}
                      </div>
                      <div className="branch_distance">
                        {currentLocation.latitude
                          ? formatDistance(
                              calculateDistance(
                                {
                                  lat: currentLocation.latitude,
                                  lng: currentLocation.longitude,
                                },
                                obj
                              )
                            )
                          : ''}
                      </div>
                    </div>
                    <div
                      style={{
                        height: '1px',
                        margin: '0px 20px',
                        backgroundColor: '#f5f6f8',
                      }}
                    />
                  </div>
                ))}
              </div>
            </div>
          </Grid>
          <Grid item xl={8} lg={8} md={8} sm={12} xs={12}>
            <div className="map_container">
              <Map
                fullscreenControl={false}
                google={props.google}
                center={selectedBranch && selectedBranch.lat && selectedBranch}
                zoom={14}
                initialCenter={
                  selectedBranch && selectedBranch.lat && selectedBranch
                }
                style={{ width: '100%', height: '100%', position: 'relative' }}
              >
                {listBranch.map((obj) => (
                  <Marker
                    position={obj}
                    onClick={onMarkerClick}
                    icon={{ url: MarkerIcon }}
                  />
                ))}
                <InfoWindow marker={activeMarker} visible>
                  <div>
                    <div className="title_marker">
                      <img
                        className="image_merchant_marker"
                        alt=""
                        src={activeBranchMarker?.image_url}
                      />
                      <div className="merchant_name_marker">
                        {selectedBranch.name}
                      </div>
                    </div>
                    <div className="merchant_address_marker">
                      {selectedBranch.address}
                    </div>
                  </div>
                </InfoWindow>
              </Map>
            </div>
          </Grid>
        </Grid>

        <div className="icon_close" onClick={props.onCloseClick}>
          <IconButton icon="close" />
        </div>
      </div>
    </BaseModal>
  );
};

export default React.memo(
  GoogleApiWrapper({
    apiKey: GOOGLE_MAP_API_KEY,
  })(BranchModal)
);
