import { put, takeEvery } from 'redux-saga/effects';
import { getListBranchesApi } from 'src/services/branch.service';

import {
  GET_LIST_BRANCH_HOME_BY_MERCHANT_ID,
  GET_LIST_BRANCH_BY_ID,
} from '../constants/branch.constant';

import {
  getListBranchHomeByMerchantIdFailure,
  getListBranchHomeByMerchantIdSuccess,
  getListBranchByIdSuccess,
  getListBranchByIdFailure,
} from '../actions/branch.action';

export function* getListBranchesByIdSaga(action) {
  const { onSuccess, page = 1, pageSize = 6, filter } = action;
  try {
    const res = yield getListBranchesApi(page, pageSize, filter);
    yield put(getListBranchByIdSuccess(res?.list_branches?.data));
    if (typeof onSuccess === 'function') {
      onSuccess(res?.list_branches?.data);
    }
  } catch (error) {
    yield put(getListBranchByIdFailure(error.message));
  }
}

export function* getListBranchesHomeByMerchantIdSaga(action) {
  const { onSuccess, page = 1, size = 3, merchantId } = action;
  try {
    const res = yield getListBranchesApi(page, size, {
      merchant_id: merchantId,
    });
    yield put(getListBranchHomeByMerchantIdSuccess(res.list_branches.data));
    if (typeof onSuccess === 'function') {
      onSuccess(res.list_branches.data);
    }
  } catch (error) {
    yield put(getListBranchHomeByMerchantIdFailure(error.message));
  }
}

export default function* branchSaga() {
  yield takeEvery(GET_LIST_BRANCH_BY_ID.HANDLER, getListBranchesByIdSaga);

  yield takeEvery(
    GET_LIST_BRANCH_HOME_BY_MERCHANT_ID.HANDLER,
    getListBranchesHomeByMerchantIdSaga
  );
}
