import { takeEvery, put, select, call } from 'redux-saga/effects';

import {
  createDeliveryFoodApi,
  getListDeliveryHistoryApi,
  getListFoodsDeliveryApi,
  getShippingFeeDeliveryApi,
} from 'src/services/delivery.service';
import {
  createDeliveryFoodFailure,
  createDeliveryFoodSuccess,
  getListDeliveryHistoryFailure,
  getListFoodsDeliveryFailure,
  getListFoodsDeliverySuccess,
  getShippingFeeFailure,
  getShippingFeeSuccess,
} from '../actions/delivery.action';

import {
  CREATE_DELIVERY_FOOD,
  GET_LIST_DELIVERY_HISTORY,
  GET_LIST_FOODS_DELIVERY,
  GET_SHIPPING_FEE,
} from '../constants/delivery.constant';
import { listFoodsDeliveryParse } from '../parser/delivery.parser';
import { getDeviceTokenSelector } from '../selectors/auth';
import { getDeviceTokenSaga } from './authSaga';

export function* getListFoodsDeliverySaga(action) {
  const { onSuccess, page = 1, pageSize = 6, filter } = action;
  try {
    let deviceToken = yield select(getDeviceTokenSelector);
    if (!deviceToken) {
      deviceToken = yield getDeviceTokenSaga();
    }
    const res = yield getListFoodsDeliveryApi(page, pageSize, filter, {
      Authorization: `JWT ${deviceToken}`,
    });
    const listMenu = listFoodsDeliveryParse(res?.list_deals?.data);
    yield put(getListFoodsDeliverySuccess(listMenu));
    if (typeof onSuccess === 'function') {
      onSuccess({
        menu: listMenu,
        total: res?.list_foods_menu?.metadata.total,
      });
    }
  } catch (error) {
    yield put(getListFoodsDeliveryFailure(error.message));
  }
}

export function* createDeliveryFoodInfoSaga(action) {
  const { deliveryFoodInfo, onError, onSuccess } = action;
  try {
    const data = yield call(createDeliveryFoodApi, deliveryFoodInfo);

    yield put(createDeliveryFoodSuccess(deliveryFoodInfo));
    if (typeof onSuccess === 'function') {
      onSuccess(data);
    }
  } catch (err) {
    if (typeof onError === 'function') {
      onError(err?.response?.data);
    }
    yield put(createDeliveryFoodFailure(err));
  }
}

export function* getShippingFeeSaga(action) {
  const { deliveryFoodInfo, onError, onSuccess } = action;
  try {
    const data = yield call(getShippingFeeDeliveryApi, deliveryFoodInfo);

    yield put(getShippingFeeSuccess(deliveryFoodInfo));
    if (typeof onSuccess === 'function') {
      onSuccess(data);
    }
  } catch (err) {
    if (typeof onError === 'function') {
      onError(err?.response?.data);
    }
    yield put(getShippingFeeFailure(err));
  }
}

export function* getListDeliveryHistorySaga(action) {
  const { onSuccess, page = 1, pageSize = 100 } = action;
  try {
    const res = yield getListDeliveryHistoryApi(page, pageSize);
    // const data = listVoucherParse(res?.list_vouchers?.data);
    const data = res?.list_delivery_foods?.data;
    // Filter voucher not use

    if (typeof onSuccess === 'function') {
      onSuccess(data);
    }
  } catch (error) {
    yield put(getListDeliveryHistoryFailure(error.message));
  }
}

export default function* orderSaga() {
  yield takeEvery(GET_LIST_FOODS_DELIVERY.HANDLER, getListFoodsDeliverySaga);
  yield takeEvery(
    GET_LIST_DELIVERY_HISTORY.HANDLER,
    getListDeliveryHistorySaga
  );
  yield takeEvery(CREATE_DELIVERY_FOOD.HANDLER, createDeliveryFoodInfoSaga);
  yield takeEvery(GET_SHIPPING_FEE.HANDLER, getShippingFeeSaga);
}
