import {
  GET_LIST_PROMOTION_HOME,
  GET_LIST_PROMOTION,
} from '../constants/promotion.constant';

export const getListPromotionHandle = (page, size, onSuccess) => ({
  type: GET_LIST_PROMOTION.HANDLER,
  page,
  size,
  onSuccess,
});

export const getListPromotionSuccess = (listDeal) => ({
  type: GET_LIST_PROMOTION.SUCCESS,
  listDeal,
});

export const getListPromotionFailure = (error) => ({
  type: GET_LIST_PROMOTION.FAILURE,
  error,
});

export const getListPromotionHomeHandle = (onSuccess) => ({
  type: GET_LIST_PROMOTION_HOME.HANDLER,
  onSuccess,
});

export const getListPromotionHomeSuccess = (listDeal) => ({
  type: GET_LIST_PROMOTION_HOME.SUCCESS,
  listDeal,
});

export const getListPromotionHomeFailure = (error) => ({
  type: GET_LIST_PROMOTION_HOME.FAILURE,
  error,
});
