export const getHappyBucketByIdHandle = (happyBucketId, onSuccess) => ({
  type: GET_HAPPY_BUCKET_BY_ID.HANDLER,
  happyBucketId,
  onSuccess,
});

export const getHappyBucketByIdSuccess = (happyBucket) => ({
  type: GET_HAPPY_BUCKET_BY_ID.SUCCESS,
  happyBucket,
});

export const getHappyBucketByIdFailure = (error) => ({
  type: GET_HAPPY_BUCKET_BY_ID.FAILURE,
  error,
});

