export const listNewsParse = (listNews) => {
  if (listNews && listNews.length > 0) {
    return listNews.map((news) => {
      return {
        id: news.id,
        imageCover: news.cover_img,
        name: news.name,
        description: news.description,
        createdAt: news.created_at,
        data: news.data,
      };
    });
  }
  return [];
};

export const voucherDetailsParse = (voucher) => {
  if (voucher) {
    return {
      ...voucher,
      id: voucher.id,
      imageUrls: voucher.image_urls,
      name: voucher.name,
    };
  }
  return [];
};
