import { put, takeLatest } from 'redux-saga/effects';

import {
  getDealByIdApi,
  getListHappyBucketApi,
} from 'src/services/deal.service';
import { listVoucherParse } from '../parser/voucher.parser';
import {
  GET_HAPPY_BUCKET_BY_ID,
  GET_LIST_HAPPY_BUCKET,
  GET_LIST_HAPPY_BUCKET_DEAL_HOME,
} from '../constants/happyBucket.constant';
import {
  getListHappyBucketDealHomeSuccess,
  getListHappyBucketDealHomeFailure,
  getHappyBucketByIdFailure,
  getHappyBucketByIdSuccess,
  getListHappyBucketFailure,
  getListHappyBucketSuccess,
} from '../actions/happyBucket.action';
import { listHappyBucketParse } from '../parser/happyBucket.parser';

export function* getHappyBucketByIdSaga(action) {
  const { onSuccess, happyBucketId } = action;
  try {
    const res = yield getDealByIdApi(happyBucketId);
    yield put(getHappyBucketByIdSuccess(res));
    if (typeof onSuccess === 'function') {
      onSuccess(res.get_deal);
    }
  } catch (error) {
    yield put(getHappyBucketByIdFailure(error.message));
  }
}

export function* getListHappyBucketDealHomeSaga(action) {
  const { onSuccess, page = 1, size = 6 } = action;
  try {
    const res = yield getListHappyBucketApi(
      page,
      size,
      { happy_bucket: true },
      [
        { field: 'order', order: 'desc' },
        { field: 'created_at', order: 'desc' },
      ]
    );
    const data = listVoucherParse(res?.list_happy_buckets?.data);

    yield put(getListHappyBucketDealHomeSuccess(data));
    if (typeof onSuccess === 'function') {
      onSuccess(data);
    }
  } catch (error) {
    yield put(getListHappyBucketDealHomeFailure(error.message));
  }
}

export function* getListHappyBucketSaga(action) {
  const { onSuccess, page = 1, pageSize = 6 } = action;
  try {
    const res = yield getListHappyBucketApi(page, pageSize);
    const data = listHappyBucketParse(res?.list_happy_buckets?.data);
    yield put(getListHappyBucketSuccess({ ...res?.list_happy_buckets, data }));
    if (typeof onSuccess === 'function') {
      onSuccess({ ...res?.list_happy_buckets, data });
    }
  } catch (error) {
    yield put(getListHappyBucketFailure(error.message));
  }
}

export default function* happyBucketSaga() {
  yield takeLatest(
    GET_LIST_HAPPY_BUCKET_DEAL_HOME.HANDLER,
    getListHappyBucketDealHomeSaga
  );
  yield takeLatest(GET_LIST_HAPPY_BUCKET.HANDLER, getListHappyBucketSaga);
  yield takeLatest(GET_HAPPY_BUCKET_BY_ID.HANDLER, getHappyBucketByIdSaga);
}
