import { put, takeLatest } from 'redux-saga/effects';

import {
  getDealByIdApi,
  // getListDealByMerchantApi,
} from 'src/services/deal.service';
import {
  getListMerchantFailure,
  getListMerchantSuccess,
} from '../actions/merchant.action';

import { GET_LIST_MERCHANT } from '../constants/merchant.constant';

export function* getListMerchantSaga(action) {
  const { onSuccess } = action;
  try {
    const res1 = yield getDealByIdApi(939);
    console.log('res1', res1);
    const res2 = yield getDealByIdApi(940);
    const listMerchant = [res1.get_deal, res2.get_deal];
    yield put(getListMerchantSuccess(listMerchant));
    if (typeof onSuccess === 'function') {
      onSuccess(listMerchant);
    }
  } catch (error) {
    yield put(getListMerchantFailure(error.message));
  }
}

export default function* merchantSaga() {
  yield takeLatest(GET_LIST_MERCHANT.HANDLER, getListMerchantSaga);
}
