/* eslint-disable import/prefer-default-export */
import React, { useCallback, useState } from 'react';
import { makeStyles, Modal as BaseModal } from '@material-ui/core';
import ModalContent from 'src/components/base/Modal';

const useStyles = makeStyles({
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    padding: '0 10px',
  },
});

export function useModal(options = {}) {
  const { size = 'medium', showed = false } = options;
  const [isShow, setShow] = useState(showed);
  const classes = useStyles();

  const show = useCallback(() => {
    setShow(true);
  }, []);

  const hide = useCallback((evt) => {
    if (evt) {
      evt.preventDefault();
      evt.stopPropagation();
    }
    setShow(false);
  }, []);

  const Modal = useCallback(
    ({ children }) => {
      if (!children) return null;
      return (
        <BaseModal
          disableBackdropClick
          open={isShow}
          onClose={hide}
          className={classes.modal}
        >
          <ModalContent className={size} onClose={hide}>
            {children}
          </ModalContent>
        </BaseModal>
      );
    },
    [isShow]
  );

  return { Modal, isShow, show, hide };
}
