import {
  GET_LIST_RECRUITMENT,
  POST_RECRUITMENT_INFO,
} from '../constants/fillRecruitment.constant';

const initialState = {
  listJobs: [],
  recruitmentInfo: {},
};

export default function fillRecruitmentReducer(state = initialState, action) {
  const { type, res } = action;
  console.log('res123', res);
  switch (type) {
    case GET_LIST_RECRUITMENT.SUCCESS:
      return {
        ...state,
        listJobs: res,
      };
    case POST_RECRUITMENT_INFO.SUCCESS:
      return {
        ...state,
        recruitmentInfo: res,
      };

    default:
      return state;
  }
}
