import { POST_COOPERATION_INFO } from '../constants/cooperation.constant';

export const requestCooperationRecruitmentHandle = (params, onSuccess) => ({
  type: POST_COOPERATION_INFO.HANDLER,
  params,
  onSuccess,
});

export const requestCooperationRecruitmentSuccess = (res) => ({
  type: POST_COOPERATION_INFO.SUCCESS,
  res,
});

export const requestCooperationRecruitmentFailure = (error) => ({
  type: POST_COOPERATION_INFO.FAILURE,
  error,
});
