/* eslint-disable import/prefer-default-export */
/* eslint-disable no-return-await */
import { API_URL } from 'src/constants';
import {
  getListDealQuery,
  getListDeliveryFoodsQuery,
} from 'src/graphql/queries/deal.query';
import APIUtils from 'src/utils/apiUtils';
import apolloClient from '../graphql/index';

export function getListFoodsDeliveryApi(page, size, filter, headers) {
  return apolloClient
    .query({
      query: getListDealQuery,
      variables: {
        page,
        size,
        filter,
      },
      context: {
        service: 'order',
        headers,
      },
    })
    .then((res) => res.data);
}
export function getListDeliveryHistoryApi(page, size, headers) {
  return apolloClient
    .query({
      query: getListDeliveryFoodsQuery,
      variables: {
        page,
        size,
      },
      context: {
        service: 'order',
        headers,
      },
    })
    .then((res) => res.data);
}

export const createDeliveryFoodApi = async (deliveryFoodInfo) => {
  return await APIUtils.post(`${API_URL}/delivery/foods`, deliveryFoodInfo);
};

export const getShippingFeeDeliveryApi = async (deliveryFoodInfo) => {
  return await APIUtils.post(`${API_URL}/delivery/shipping`, deliveryFoodInfo);
};
