import { takeLatest, call, put } from 'redux-saga/effects';
import { requestBookingApi } from 'src/services/booking.service';
import {
  requestBookingFailure,
  requestBookingSuccess,
} from '../actions/booking.action';

import { REQUEST_BOOKING } from '../constants/booking.constant';

export function* requestBookingSaga({ bookingInfo, onSuccess }) {
  try {
    const res = yield call(requestBookingApi, bookingInfo);
    if (typeof onSuccess === 'function') {
      onSuccess(res);
    }
    yield put(requestBookingSuccess(res));
  } catch (err) {
    yield put(requestBookingFailure(err));
  }
}

export default function* bookingSaga() {
  yield takeLatest(REQUEST_BOOKING.HANDLER, requestBookingSaga);
}
