/* eslint-disable no-return-await */
import { getListFoodsMenuQuery } from 'src/graphql/queries/order.query';
import { API_URL } from 'src/constants';
import APIUtils from 'src/utils/apiUtils';
import apolloClient from '../graphql/index';

export function getListFoodsMenuApi(page, size, filter, headers) {
  return apolloClient
    .query({
      query: getListFoodsMenuQuery,
      variables: {
        page,
        size,
        filter,
      },
      context: {
        service: 'order',
        headers,
      },
    })
    .then((res) => res.data);
}

export const requestBookingsFoodsApi = async (data) => {
  return await APIUtils.post(`${API_URL}/bookings/foods`, data, {});
};
