/* eslint-disable no-unused-vars */
/* eslint-disable react/jsx-props-no-spreading */
import React, { useCallback, useEffect, useRef, useState } from 'react';
import { Title } from 'src/components/base';
import FormBuilder from 'src/components/FormBuilder/FormBuilder';
import './UpdateUserInfo.scss';
import * as Yup from 'yup';
import { updateFirstTimeUserInfoHandle } from 'src/redux/actions/auth.action';
import { useDispatch } from 'react-redux';
import { addModal } from 'src/redux/actions/ui.action';
import { MODAL_TYPE } from 'src/constants';
import { Formik } from 'formik';
import {
  FormControl,
  FormHelperText,
  FormLabel,
  Grid,
} from '@material-ui/core';
import { Input, ImageUpload } from 'src/components/base/Inputs';
import { AuthFormButton } from '../Button';

const UpdateUserInfo = (props) => {
  const [dateModalOpen, setDateModalOpen] = useState(false);
  const [formInfo, setFormInfo] = useState({
    avatar: '',
    name: '',
    cmnd: '',
    phone: props?.data?.phone,
    email: '',
    dob: '',
    address: '',
  });
  const dateModalRef = useRef();

  const dispatch = useDispatch();
  const handleClickOutside = useCallback(
    (event) => {
      if (
        dateModalOpen &&
        dateModalRef &&
        !dateModalRef?.current?.contains(event.target)
      ) {
        setDateModalOpen(false);
      }
    },
    [dateModalRef, dateModalOpen]
  );
  useEffect(() => {
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [handleClickOutside]);

  const updateInfoSchema = Yup.object().shape({
    name: Yup.string().required('Vui lng nhp h v tn'),
    email: Yup.string()
      .email('nh dng email khng hp l')
      .required('Vui lng nhp email'),
  });

  const onSubmit = useCallback((values) => {
    dispatch(
      updateFirstTimeUserInfoHandle(values, () => {
        dispatch(addModal({ type: MODAL_TYPE.REGISTER_ACCOUNT_COMPLETE }));
      })
    );
  }, []);

  const renderInputItem = (data) => {
    return (
      <FormControl className="form-widget">
        {data.label && (
          <FormLabel required={data.required} className="form-label">
            {data.label}
          </FormLabel>
        )}
        <div className="form-input">
          <Input {...data} />
          {data.error && (
            <FormHelperText className="error">{data.error}</FormHelperText>
          )}
        </div>
      </FormControl>
    );
  };

  return (
    <div className="update-info-form">
      <Title>Cp nht thng tin</Title>
      <Formik
        initialValues={formInfo}
        onSubmit={onSubmit}
        validationSchema={updateInfoSchema}
      >
        {({ handleSubmit, errors, setFieldValue, values }) => {
          console.log('Values>>>>', values);
          return (
            <form className="form-builder" onSubmit={handleSubmit}>
              <div style={{ marginBottom: '10px' }}>
                <ImageUpload
                  name="avatar"
                  setFieldValue={setFieldValue}
                  {...props}
                />
              </div>
              <Grid spacing={3} container>
                <Grid item xl={6} lg={6} md={6} sm={12} xs={12}>
                  {renderInputItem({
                    label: 'H tn*',
                    className: 'form_info_name',
                    error: errors.name,
                    onChange: (event) => {
                      setFieldValue('name', event.target.value);
                    },
                  })}
                </Grid>
                <Grid item xl={6} lg={6} md={6} sm={12} xs={12}>
                  {renderInputItem({
                    label: 'CMND',
                    className: 'form_info_identify',
                    onChange: (event) => {
                      setFieldValue('dob', event.target.value);
                    },
                  })}
                </Grid>
                <Grid item xl={6} lg={6} md={6} sm={12} xs={12}>
                  {renderInputItem({
                    label: 'S in thoi',
                    className: 'form_info_phone_number',
                    readOnly: true,
                    value: values.phone,
                  })}
                </Grid>
                <Grid item xl={6} lg={6} md={6} sm={12} xs={12}>
                  {renderInputItem({
                    label: 'Email',
                    className: 'form_info_email',
                    error: errors.email,
                    onChange: (event) => {
                      setFieldValue('email', event.target.value);
                    },
                  })}
                </Grid>
                <Grid item xl={6} lg={6} md={6} sm={12} xs={12}>
                  {renderInputItem({
                    label: 'Ngy thng nm sinh',
                    className: 'form_info_dob',
                    placeholder: 'dd/mm/yyyy',
                    onChange: (event) => {
                      setFieldValue('dob', event.target.value);
                    },
                  })}
                </Grid>

                <Grid item xl={12} lg={12} md={12} sm={12} xs={12}>
                  {renderInputItem({
                    label: 'a ch',
                    className: 'form_info_address',
                    onChange: (event) => {
                      setFieldValue('address', event.target.value);
                    },
                  })}
                </Grid>
              </Grid>
              <div style={{ marginBottom: '20px', marginTop: '20px' }}>
                <AuthFormButton title="Hon thnh" />
              </div>
            </form>
          );
        }}
      </Formik>
    </div>
  );
};

export default UpdateUserInfo;
