/* eslint-disable no-unused-vars */
/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
import React from 'react';
import _ from 'lodash';
import { Box, Grid, SwipeableDrawer, Hidden } from '@material-ui/core';
import get from 'lodash/get';
import { Link } from 'react-router-dom';
import { TransactionIcon, LogoutIcon, CreditCardIcon } from 'src/assets/icons';
import { addModal } from 'src/redux/actions/ui.action';
import { MODAL_TYPE } from 'src/constants';
import Drawer from 'src/layouts/header/components/Drawer/Drawer';
import { connect } from 'react-redux';

import logoHome from '../../assets/icons/logo-home.png';
import iconCart from '../../assets/icons/icon-cart.png';
import avatarDefault from '../../assets/icons/avatar-default.png';
import aboutIcon from '../../assets/icons/icon-nav-about.png';
import menuIcon from '../../assets/icons/icon-nav-menu.png';
import happyBucketIcon from '../../assets/icons/icon-nav-happy-bucket.png';
import voucherIcon from '../../assets/icons/icon-nav-voucher.png';
import bookingIcon from '../../assets/icons/icon-nav-booking.png';
import deliveryIcon from '../../assets/icons/icon-nav-delivery.png';
import partnerIcon from '../../assets/icons/icon-nav-partner.png';
import promotionIcon from '../../assets/icons/icon-nav-promotion.png';
import recruitmentIcon from '../../assets/icons/icon-nav-recruitment.png';
import './Header.scss';
import SearchContainer from '../../components/Search/Search';
import NotificationContainer from '../../components/Notification/Notification.container';
import Contact from './components/contact/Contact';

const categories = [
  { id: 1, name: 'V chng ti', icon: aboutIcon, path: '/about' },
  { id: 2, name: 'Menu', icon: menuIcon, path: '/order' },
  {
    id: 3,
    name: 'Happy Bucket',
    icon: happyBucketIcon,
    path: '/happy-bucket',
  },
  { id: 4, name: 'Phiu u i', icon: voucherIcon, path: '/voucher' },
  { id: 5, name: 'Booking', icon: bookingIcon, path: '/booking' },
  { id: 6, name: 'Delivery', icon: deliveryIcon, path: '/delivery' },
  { id: 7, name: 'Lm i tc', icon: partnerIcon, path: '/partner' },
  { id: 8, name: 'Khuyn mi', icon: promotionIcon, path: '/promotion' },
  {
    id: 9,
    name: 'Tuyn dng',
    icon: recruitmentIcon,
    path: '/recruitment',
  },
];
class Header extends React.Component {
  constructor(props) {
    super(props);
    this.state = { mobileOpen: false };
  }

  handleDrawerToggle = () => {
    this.setState((state) => ({ mobileOpen: !state.mobileOpen }));
  };

  login = (evt) => {
    evt.preventDefault();
    this.props.login();
  };

  register = (evt) => {
    evt.preventDefault();
    this.props.login();
  };

  transactionClick = (evt) => {
    evt.preventDefault();
    this.props.history.push('/history');
  };

  userInfoClick = (evt) => {
    evt.preventDefault();
    this.props.history.replace('/user');
  };

  userInfoClick = (evt) => {
    evt.preventDefault();
    this.props.history.replace('/user');
  };

  render() {
    const pathName = get(window.location, 'pathname', '/');
    const isShowMenu = !pathName.includes('categories');

    const {
      userInfo: user,
      settings,
      listNotifications,
      loadMoreHandle,
      vouchers,
      totalNotification,
    } = this.props;
    const { companyAppAndroid, companyAppIos } = settings || {};
    const companyHotline = '0287 309 79 49';

    const renderMenuItem = (cate) => (
      <Grid key={cate.id}>
        <div className="menu_item_container">
          <Link style={{ flex: 1 }} to={`${cate.path}`}>
            <div className="menu_item_content md-3 lg-3 sm-6 ">
              {cate.icon && cate.name && (
                <>
                  <img src={cate.icon} alt="cate" className="category_logo" />
                  <p
                    className="menu_name"
                    style={{ textTransform: 'capitalize' }}
                  >
                    {cate.name}
                  </p>
                </>
              )}
            </div>
          </Link>
        </div>
      </Grid>
    );
    const renderMenu = () => {
      return (
        <Grid key="menu-1" container justify="space-between">
          {categories.map((cate) => renderMenuItem(cate))}
        </Grid>
      );
    };

    const cartNumber =
      (this.props.vouchers?.length || 0) +
      this.props.deliveries?.reduce((total, delivery) => {
        return total + delivery?.data?.length || 0;
      }, 0);
    return (
      <header className="header_container">
        <Contact
          companyHotline={companyHotline}
          user={user}
          onNavigateToCart={this.props.onNavigateToCart}
          vouchers={this.props.vouchers}
          deliveries={this.props.deliveries}
          companyAppAndroid={companyAppAndroid}
          companyAppIos={companyAppIos}
          handleDrawerToggle={this.handleDrawerToggle}
          onNotificationClick={this.props.onNotificationClick}
          listNotifications={listNotifications}
          loadMoreHandle={loadMoreHandle}
          totalNotification={totalNotification}
        />
        <Hidden smUp>
          <Grid item sm={12}>
            <SearchContainer handleSubmit={() => {}} />
          </Grid>
          <SwipeableDrawer
            variant="temporary"
            anchor="left"
            open={this.state.mobileOpen}
            onOpen={this.handleDrawerToggle}
            onClose={this.handleDrawerToggle}
          >
            <Drawer
              logout={this.props.logout}
              user={user}
              login={this.login}
              categories={categories}
              navigateCreditCard={this.props.navigateCreditCard}
              onTransactionClick={this.transactionClick}
            />
          </SwipeableDrawer>
        </Hidden>
        <Hidden xsDown>
          <nav>
            <div className="container">
              <Grid
                container
                direction="row"
                alignItems="center"
                className="menu_container"
              >
                <Grid item sm={2} md={2} lg={2} xl={2}>
                  <div className="logo_container">
                    <div className="logoHome">
                      <Link to="/">
                        <img
                          src={logoHome}
                          className="image_logo_home"
                          alt="logo"
                        />
                      </Link>
                    </div>
                  </div>
                </Grid>

                <Grid item sm={5} md={5} lg={6} xl={6}>
                  <div className="search_temp_container">
                    <SearchContainer
                      list={[
                        { id: '0', label: 'lemon' },
                        { id: '1', label: 'banana' },
                        { id: '2', label: 'apple' },
                        { id: '3', label: 'orange' },
                        { id: '4', label: 'coconut' },
                      ]}
                    />
                  </div>
                </Grid>

                <Grid item sm={5} md={5} xl={4} lg={4}>
                  <div className="account_container">
                    <Hidden xsDown>
                      <div
                        onClick={this.props.onNavigateToCart}
                        to="/cart"
                        className="link"
                      >
                        {cartNumber > 0 ? (
                          <div className="badge">
                            <p>{cartNumber}</p>
                          </div>
                        ) : null}
                        <img src={iconCart} className="cart_ic" alt="icon" />
                      </div>
                    </Hidden>
                    <Hidden xsDown>
                      <NotificationContainer
                        onClick={this.props.onNotificationClick}
                        notifications={listNotifications}
                        loadMoreHandle={loadMoreHandle}
                        totalNotification={totalNotification}
                      />
                    </Hidden>
                    <div className="user_container">
                      <img
                        src={
                          !_.isEmpty(user) && user.imageUrl
                            ? user.imageUrl
                            : avatarDefault
                        }
                        className="avatar_ic"
                        alt="avatar"
                      />
                      {this.props.accessToken ? (
                        <Link to="/user">
                          <Box
                            className="box-user"
                            display="flex"
                            alignItems="center"
                          >
                            <p
                              className="user_name"
                              onClick={this.userInfoClick}
                            >
                              {user?.name}
                            </p>

                            {this.props.accessToken && (
                              <div className="user_action_container">
                                <div className="user-action-content">
                                  <Box
                                    display="flex"
                                    style={{ cursor: 'pointer' }}
                                    alignItems="center"
                                    className="transaction_container"
                                    onClick={this.transactionClick}
                                  >
                                    <TransactionIcon className="transaction_icon" />
                                    <p className="transaction_title">
                                      Lch s dch v
                                    </p>
                                  </Box>
                                  <Box
                                    display="flex"
                                    style={{
                                      marginTop: '10px',
                                      cursor: 'pointer',
                                    }}
                                    className="credit_card_container"
                                    alignItems="center"
                                    onClick={this.props.navigateCreditCard}
                                  >
                                    <CreditCardIcon className="credit_card_icon" />
                                    <p className="credit_card_title">
                                      Thng tin th
                                    </p>
                                  </Box>
                                  <Box
                                    display="flex"
                                    style={{
                                      marginTop: '10px',
                                      cursor: 'pointer',
                                    }}
                                    className="logout_container"
                                    alignItems="center"
                                    onClick={this.props.logout}
                                  >
                                    <LogoutIcon className="logout_icon" />
                                    <p className="logout_title">ng xut</p>
                                  </Box>
                                </div>
                              </div>
                            )}
                          </Box>
                        </Link>
                      ) : (
                        <div className="regiter_or_login">
                          <p onClick={this.login}>ng nhp</p>
                        </div>
                      )}
                    </div>

                    <div className="box-switch-language">
                      <p className="text-languages active">VI</p>
                      <p className="text-languages">|</p>
                      <p className="text-languages">EN</p>
                    </div>
                  </div>
                </Grid>
              </Grid>

              {isShowMenu && renderMenu()}
            </div>
          </nav>
        </Hidden>
      </header>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    login: () => dispatch(addModal({ type: MODAL_TYPE.MODAL_LOGIN })),
    register: () => dispatch(addModal({ type: MODAL_TYPE.MODAL_REGISTER })),
  };
};

export default connect(null, mapDispatchToProps)(Header);
