import { takeLatest, call, put, select } from 'redux-saga/effects';
import {
  requestJobsRecruitmentApi,
  requestJobsRecruitmentInfoApi,
} from 'src/services/fillRecruitment.service';
import {
  requestListJobsRecruitmentFailure,
  requestListJobsRecruitmentSuccess,
  requestJobsRecruitmentInfoFailure,
  requestJobsRecruitmentInfoSuccess,
} from '../actions/fillRecruitment.action';

import {
  GET_LIST_RECRUITMENT,
  POST_RECRUITMENT_INFO,
} from '../constants/fillRecruitment.constant';
import { getDeviceTokenSelector } from '../selectors/auth';
import { getDeviceTokenSaga } from './authSaga';

export function* requestListJobsRecruitmentSaga() {
  try {
    let deviceToken = yield select(getDeviceTokenSelector);
    if (!deviceToken) {
      deviceToken = yield getDeviceTokenSaga();
    }

    const res = yield call(requestJobsRecruitmentApi, {
      Authorization: `JWT ${deviceToken}`,
    });
    yield put(requestListJobsRecruitmentSuccess(res));
  } catch (err) {
    yield put(requestListJobsRecruitmentFailure(err));
  }
}

export function* requestRecruitmentInfoSaga({ params, onSuccess }) {
  try {
    let deviceToken = yield select(getDeviceTokenSelector);
    if (!deviceToken) {
      deviceToken = yield getDeviceTokenSaga();
    }

    const res = yield call(requestJobsRecruitmentInfoApi, params, {
      Authorization: `JWT ${deviceToken}`,
    });
    if (typeof onSuccess === 'function') {
      onSuccess(res);
    }
    yield put(requestJobsRecruitmentInfoSuccess(res));
  } catch (err) {
    yield put(requestJobsRecruitmentInfoFailure(err));
  }
}

export default function* bookingSaga() {
  yield takeLatest(
    GET_LIST_RECRUITMENT.HANDLER,
    requestListJobsRecruitmentSaga
  );
  yield takeLatest(POST_RECRUITMENT_INFO.HANDLER, requestRecruitmentInfoSaga);
}
