import React, { useState, useRef, useEffect } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Hidden } from '@material-ui/core';
import {
  getVouchersBySearchHandle,
  setKeywordForSearchHandle,
} from 'src/redux/actions/voucher.action';
import {
  getSuggestionsSelector,
  getTotalSearchSelector,
  getKeywordSelector,
} from 'src/redux/selectors/voucher.selector';
import { useHistory } from 'react-router-dom';
import './Search.scss';
import { SearchIcon } from 'src/assets/icons';

const Search = (props) => {
  const [keyword, setKeyword] = useState('');
  const [suggestions, setSuggestions] = useState([]);
  const typingTimeoutRef = useRef(null);
  const dropdownRef = useRef(null);
  const history = useHistory();

  useEffect(() => {
    /**
     * Alert if clicked on outside of element
     */
    const handleClickOutside = (event) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
        setSuggestions([]);
      }
    };

    // Bind the event listener
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      // Unbind the event listener on clean up
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [dropdownRef]);

  const onChangeKeyword = (e) => {
    const { value } = e.target;
    setKeyword(value);

    if (typingTimeoutRef.current) {
      clearTimeout(typingTimeoutRef.current);
    }

    typingTimeoutRef.current = setTimeout(() => {
      props.getVouchersBySearchHandle(value, 1, 5, (res) => {
        setSuggestions(res);
        console.log(
          ' ~ file: Search.js ~ line 84 ~ props.getVouchersBySearchHandle ~ res',
          res
        );
      });
    }, 1000);
  };

  const handleSubmit = () => {
    setSuggestions([]);
    props.setKeywordForSearchHandle(keyword);
    // props.getVouchersBySearchHandle(keyword, 1, 12, false);

    history.push(`/search`);
  };

  const enterPressed = (event) => {
    const code = event.keyCode || event.which;
    if (code === 13) {
      handleSubmit();
    }
  };

  const onItemClick = (item) => {
    console.log(' ~ file: Search.js ~ line 109 ~ onItemClick ~ id', item);

    if (item.type_deal === 1) {
      history.push(`/booking/${item.id}`);
    }
    if (item.type_deal === 2) {
      history.push(`/voucher/${item.id}`);
    }
    if (item.type_deal === 3) {
      history.push(`/delivery/${item.id}`);
    }
  };

  return (
    <div className="search_container">
      <Hidden smUp>
        <div style={{ marginLeft: '10px' }}>
          <SearchIcon className="icon_search_left" />
        </div>
      </Hidden>
      <input
        value={keyword}
        placeholder="Tm theo tn, danh mc"
        className="input-container"
        onChange={(e) => onChangeKeyword(e)}
        onKeyPress={enterPressed}
      />
      <Hidden xsDown>
        <button type="button" className="btn_search" onClick={handleSubmit}>
          <SearchIcon className="icon_search" />
        </button>
      </Hidden>
      {suggestions.length > 0 && keyword !== '' ? (
        <div className="dropdown_search" ref={dropdownRef}>
          {suggestions.map((el) => (
            <div onClick={() => onItemClick(el)} className="dropdown-item">
              <p className="suggest-item"> {el.name}</p>
            </div>
          ))}
        </div>
      ) : null}
    </div>
  );
};

const mapStateToProps = (state) => ({
  suggestions: getSuggestionsSelector(state),
  totalSearch: getTotalSearchSelector(state),
  keyword: getKeywordSelector(state),
});

const mapDispatchToProps = (dispatch) =>
  bindActionCreators(
    { getVouchersBySearchHandle, setKeywordForSearchHandle },
    dispatch
  );

export default connect(mapStateToProps, mapDispatchToProps)(React.memo(Search));
