/* eslint-disable import/prefer-default-export */
export function convertCurrency(number, unit = '') {
  if (!number) {
    return 0;
  }
  let num = number.toString().replace(/[^0-9]/g, '');
  num = parseInt(num, 10);
  return (
    num
      .toString()
      .replace(/./g, (c, i, a) =>
        i > 0 && c !== '.' && (a.length - i) % 3 === 0 ? `.${c}` : c
      ) + unit
  );
}
