import {
  CREATE_DELIVERY_FOOD,
  GET_LIST_FOODS_DELIVERY,
  GET_SHIPPING_FEE,
  GET_LIST_DELIVERY_HISTORY,
} from '../constants/delivery.constant';

export const getListFoodsDeliveryHandle = (
  page,
  pageSize,
  filter,
  onSuccess
) => ({
  type: GET_LIST_FOODS_DELIVERY.HANDLER,
  page,
  pageSize,
  filter,
  onSuccess,
});

export const getListFoodsDeliverySuccess = (listMenu) => ({
  type: GET_LIST_FOODS_DELIVERY.SUCCESS,
  listMenu,
});

export const getListFoodsDeliveryFailure = (error) => ({
  type: GET_LIST_FOODS_DELIVERY.FAILURE,
  error,
});

export const createDeliveryFoodHandle = (
  deliveryFoodInfo,
  onSuccess,
  onError
) => ({
  type: CREATE_DELIVERY_FOOD.HANDLER,
  deliveryFoodInfo,
  onSuccess,
  onError,
});

export const createDeliveryFoodSuccess = (deliveryFoodInfo) => ({
  type: CREATE_DELIVERY_FOOD.SUCCESS,
  deliveryFoodInfo,
});

export const createDeliveryFoodFailure = (error) => ({
  type: CREATE_DELIVERY_FOOD.FAILURE,
  error,
});

export const getShippingFeeHandle = (deliveryFoodInfo, onSuccess, onError) => ({
  type: GET_SHIPPING_FEE.HANDLER,
  deliveryFoodInfo,
  onSuccess,
  onError,
});

export const getShippingFeeSuccess = (deliveryFoodInfo) => ({
  type: GET_SHIPPING_FEE.SUCCESS,
  deliveryFoodInfo,
});

export const getShippingFeeFailure = (error) => ({
  type: GET_SHIPPING_FEE.FAILURE,
  error,
});

export const getListDeliveryHistoryHandle = ({
  page,
  pageSize,
  onSuccess,
}) => ({
  type: GET_LIST_DELIVERY_HISTORY.HANDLER,
  page,
  pageSize,
  onSuccess,
});

export const getListDeliveryHistorySuccess = (listVoucher) => ({
  type: GET_LIST_DELIVERY_HISTORY.SUCCESS,
  listVoucher,
});

export const getListDeliveryHistoryFailure = (error) => ({
  type: GET_LIST_DELIVERY_HISTORY.FAILURE,
  error,
});
