import { GET_HAPPY_BUCKET_BY_ID } from '../constants/happyBucket.constant';
import {
  GET_LIST_RECRUITMENT,
  GET_LIST_RECRUITMENT_HOME,
} from '../constants/recruitment.constant';

export const getRecruitmentByIdHandle = (happyBucketId, onSuccess) => ({
  type: GET_HAPPY_BUCKET_BY_ID.HANDLER,
  happyBucketId,
  onSuccess,
});

export const getRecruitmentByIdSuccess = (happyBucket) => ({
  type: GET_HAPPY_BUCKET_BY_ID.SUCCESS,
  happyBucket,
});

export const getRecruitmentByIdFailure = (error) => ({
  type: GET_HAPPY_BUCKET_BY_ID.FAILURE,
  error,
});

export const getListRecruitmentHomeHandle = (onSuccess) => ({
  type: GET_LIST_RECRUITMENT_HOME.HANDLER,
  onSuccess,
});

export const getListRecruitmentHomeSuccess = (listDeal) => ({
  type: GET_LIST_RECRUITMENT_HOME.SUCCESS,
  listDeal,
});

export const getListRecruitmentHomeFailure = (error) => ({
  type: GET_LIST_RECRUITMENT_HOME.FAILURE,
  error,
});

export const getListRecruitmentHandle = (page, size, onSuccess) => ({
  type: GET_LIST_RECRUITMENT.HANDLER,
  page,
  size,
  onSuccess,
});

export const getListRecruitmentSuccess = (listDeal) => ({
  type: GET_LIST_RECRUITMENT.SUCCESS,
  listDeal,
});

export const getListRecruitmentFailure = (error) => ({
  type: GET_LIST_RECRUITMENT.FAILURE,
  error,
});
