import { LOGOUT } from '../constants/auth.constant';
import {
  GET_LIST_BANNER,
  GET_LIST_NOTIFICATIONS,
  GET_READ_NOTIFICATION,
  SET_CURRENT_LOCATION,
} from '../constants/common.constant';

const initialState = {
  listBanner: [],
  listNotifications: [],
  totalNotification: 0,
  currentLocation: {},
};

export default function commonReducer(state = initialState, action) {
  const { type } = action;
  switch (type) {
    case GET_LIST_BANNER.SUCCESS: {
      return {
        ...state,
        listBanner: action.listBanner,
      };
    }
    case SET_CURRENT_LOCATION: {
      return {
        ...state,
        currentLocation: action.location,
      };
    }
    case GET_LIST_NOTIFICATIONS.SUCCESS: {
      const { data, page, total } = action.payload;
      let newList = state.listNotifications.slice();
      if (page === 1) {
        newList = data;
      } else {
        newList.push(...data);
      }

      return {
        ...state,
        listNotifications: newList,
        totalNotification: total,
      };
    }

    case GET_READ_NOTIFICATION.SUCCESS: {
      const { payload } = action;
      let total = state.totalNotification;
      const newList = state.listNotifications.slice();
      const index = newList.findIndex((el) => el.id === payload.id);

      if (
        index !== -1 &&
        (newList[index].is_read === false || newList[index].is_seen === false)
      ) {
        total = total > 0 ? total - 1 : 0;
        newList[index].is_read = true;
        newList[index].is_seen = true;
      }

      return {
        ...state,
        listNotifications: newList,
        totalNotification: total,
      };
    }
    case LOGOUT.HANDLER: {
      return {
        ...state,
        listNotifications: [],
        totalNotification: 0,
      };
    }

    default:
      return state;
  }
}
