import moment from 'moment';

export function formatDateTime(dateTime, format) {
  return moment(dateTime).format(format || 'DD/MM/YYYY');
}

export function timeFromNow(dateTime) {
  moment.locale('vi');
  return moment(dateTime).fromNow();
}

export const isExpiredDate = (date) => {
  if (date) {
    const current = moment().format();

    return moment(date).isBefore(current);
  }
  return false;
};
