/* eslint-disable radix */
import React, { useState } from 'react';
import './requestModal.scss';
import { useModal } from 'src/hooks/useModal';
import { Button, withStyles } from '@material-ui/core';
import {
  FolderIcon,
  ForkIcon,
  TableIcon,
  LockIcon,
  LocationIcon,
  CreditCardIcon,
} from 'src/assets/icons';
import { requestBookingsFoodsHandle } from 'src/redux/actions/order.action';
import moment from 'moment';
import { useSelector, useDispatch } from 'react-redux';
import {
  getUserInfoSelector,
  getAccessTokenSelector,
} from 'src/redux/selectors/auth';
import { MODAL_TYPE } from 'src/constants';
import { addModal } from 'src/redux/actions/ui.action';

const StyledButton = withStyles(() => ({
  root: {
    color: '#FFF',
    textTransform: 'unset',
    width: '15rem',
    background: '#FE5466',
    borderRadius: '5px',
    border: 'none',
    display: 'flex',
    alignSelf: 'center',
    marginTop: '8px',
    margin: '0 auto',
    fontFamily: 'Google Sans',
  },
}))(Button);

const RequestModal = (props) => {
  const dispatch = useDispatch();
  const { Modal, show, hide } = useModal({ size: 'medium' });
  const userInfo = useSelector(getUserInfoSelector);
  const { listSelected, branch } = props;
  const accessToken = useSelector(getAccessTokenSelector);

  const [paramsRequest, setParamsRequest] = useState({
    merchant_id: 934,
    branch_id: 1307,
    table_number: '',
  });

  const onRequest = () => {
    const currentTime = moment().utc().format();
    const foods = [];
    listSelected.forEach((el) => {
      foods.push({ foodmenu_id: el.id, quanity: el.quantity });
    });

    dispatch(
      requestBookingsFoodsHandle(
        {
          ...paramsRequest,
          user_id: userInfo?.id,
          booking_time: currentTime,
          foods,
        },
        () => {
          hide();
          dispatch(
            addModal({
              type: MODAL_TYPE.ALERT_SUCCESS,
              data: {
                title: 'Yu cu gi mn thnh cng',
                subTitle: `Qu khch va yu cu gi mn thnh cng ti nh hng ${branch.name}. Chng ti s lin h li ngay.`,
                titleButton: 'Hon tt',
              },
            })
          );
        }
      )
    );
  };

  const onChangeCode = (evt) => {
    const regex = /^[0-9]+$/.test(evt.target.value);
    if (regex === true || evt.target.value === '') {
      setParamsRequest({
        ...paramsRequest,
        table_number: evt.target.value === '' ? '' : parseInt(evt.target.value),
      });
    }
  };

  return (
    <>
      <StyledButton
        variant="outlined"
        onClick={() => {
          if (accessToken) {
            show();
          } else {
            dispatch(
              addModal({
                type: MODAL_TYPE.FORCE_USER_LOGIN,
                listSelected,
              })
            );
          }
        }}
      >
        Yu cu gi mn
      </StyledButton>

      <Modal>
        {accessToken ? (
          <div className="request-modal-container">
            <p className="header-title">Gi mn ti nh hng</p>

            <div className="row-section">
              <LocationIcon />
              <div className="right-content">
                <p className="label">Chn chi nhnh ang s dng</p>
                <input
                  name="branch"
                  className="input input-branch"
                  defaultValue={branch?.address}
                />
              </div>
            </div>

            <div className="row-section">
              <TableIcon />
              <div className="right-content">
                <p className="label">S bn</p>
                <input
                  name="table-quantity"
                  className="input"
                  placeholder="Nhp s bn ca qu khch ti nh hng"
                  value={
                    paramsRequest.table_number === ''
                      ? ''
                      : paramsRequest.table_number
                  }
                  onChange={onChangeCode}
                />
              </div>
            </div>

            <div className="row-section between align-center">
              <div className="credit-section">
                <CreditCardIcon className="credit-icon" />
                <p className="orderer">Ngi t ch:&nbsp;</p>
                <p className="orderer-name">{userInfo?.name}</p>
              </div>

              <LockIcon />
            </div>

            <div className="row-section">
              <FolderIcon className="folder-icon" />
              <div className="right-content">
                <p className="label inline">Ghi ch</p>
                <p className="inline">(khng bt buc)</p>
                <input
                  name="table-quantity"
                  className="input"
                  placeholder="Nhp ghi ch"
                />
              </div>
            </div>

            <div className="row-section align-center">
              <ForkIcon className="fork-icon" />
              <p className="label-number">S lng mn:&nbsp;</p>
              <p className="number">{listSelected.length}</p>
            </div>

            <StyledButton variant="outlined" onClick={() => onRequest()}>
              Gi yu cu
            </StyledButton>
          </div>
        ) : (
          <div />
        )}
      </Modal>
    </>
  );
};

export default React.memo(RequestModal);
