import React from 'react';
import { BellIcon } from 'src/assets/icons';
import { timeFromNow } from 'src/utils/dateTime';
import './NotificationItem.scss';
import { Link } from 'react-router-dom';
import classNames from 'classnames';

const NotificationItem = (props) => {
  const { item } = props;
  return (
    <Link
      to={{ pathname: `/notification/${item.id}`, state: { id: item.id } }}
      replace
      style={{ flex: 1 }}
    >
      <div
        className={classNames('notification-item-container', {
          notification_read: item.is_read,
          notification_unread: !item.is_read,
        })}
      >
        <p className="title-txt">{item?.title}</p>
        <p className="content-txt">{item?.content}</p>
        <div className="time-content">
          <div className="bell-icon">
            <BellIcon />
          </div>

          <p className="time-txt">{timeFromNow(item?.created_at)}</p>
        </div>
      </div>
    </Link>
  );
};

export default NotificationItem;
