/* eslint-disable no-unused-vars */
import React from 'react';
import _ from 'lodash';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import PhotoIcon from '@material-ui/icons/PhotoOutlined';
import Divider from '@material-ui/core/Divider';
import './Drawer.scss';
import { Link } from 'react-router-dom';
import { TransactionIcon, LogoutIcon, CreditCardIcon } from 'src/assets/icons';
import avatarDefault from '../../../../assets/icons/avatar-default.png';

class Drawer extends React.Component {
  render() {
    const { user, categories, onClick } = this.props;
    const username = () => {
      if (!user.name) {
        return user.phoneNumber;
      }
      const name =
        user.name.length > 15 ? `${user.name.slice(0, 12)}...` : user.name;
      return name;
    };
    return (
      // eslint-disable-next-line jsx-a11y/no-static-element-interactions,jsx-a11y/click-events-have-key-events
      <div className="drawer_container" onClick={onClick}>
        {!_.isEmpty(user) ? (
          <List>
            <ListItem button className="user_name">
              <img
                src={
                  !_.isEmpty(user) && user.image_url
                    ? user.image_url
                    : avatarDefault
                }
                className="avatar"
                alt="avatar"
              />
              {username()}
            </ListItem>
            <div
              className="transaction_container"
              onClick={this.props.onTransactionClick}
            >
              <TransactionIcon className="transaction_icon" />
              <p className="transaction_title">Lch s dch v</p>
            </div>
            <div
              className="credit_card_container"
              onClick={this.props.navigateCreditCard}
            >
              <CreditCardIcon className="credit_card_icon" />
              <p className="credit_card_title">Thng tin th</p>
            </div>
            <div className="logout_container" onClick={this.props.logout}>
              <LogoutIcon className="logout_icon" />
              <div className="logout_title">ng xut</div>
            </div>
          </List>
        ) : (
          <List>
            <div className="logout_container" onClick={this.props.login}>
              <div className="login_title">ng nhp</div>
            </div>
          </List>
        )}
        <Divider />
        <List>
          {categories.map((cate, key) => (
            // eslint-disable-next-line react/no-array-index-key
            <ListItem button key={key}>
              <Link
                className="category_name"
                to={`${cate.path}`}
                title={cate.name}
              >
                {cate.icon ? (
                  <img src={cate.icon} className="cate_logo" alt={cate.name} />
                ) : (
                  <PhotoIcon className="cate_logo" />
                )}
                {cate.name}
              </Link>
            </ListItem>
          ))}
        </List>
      </div>
    );
  }
}

export default Drawer;
