import { takeLatest, takeEvery, put, call, select } from 'redux-saga/effects';
import {
  getListFoodsMenuApi,
  requestBookingsFoodsApi,
} from 'src/services/order.service';
import {
  GET_LIST_FOODS_MENU,
  REQUEST_BOOKINGS_FOODS,
} from '../constants/order.constant';

import {
  getListFoodsMenuSuccess,
  getListFoodsMenuFailure,
  requestBookingsFoodsSuccess,
  requestBookingsFoodsFailure,
} from '../actions/order.action';
import { listFoodsMenuParse } from '../parser/order.parser';
import { getDeviceTokenSelector } from '../selectors/auth';
import { getDeviceTokenSaga } from './authSaga';

export function* getListFoodsMenuSaga(action) {
  const { onSuccess, page = 1, pageSize = 6, filter } = action;
  try {
    let deviceToken = yield select(getDeviceTokenSelector);
    if (!deviceToken) {
      deviceToken = yield getDeviceTokenSaga();
    }
    const res = yield getListFoodsMenuApi(page, pageSize, filter, {
      Authorization: `JWT ${deviceToken}`,
    });
    const listMenu = listFoodsMenuParse(res?.list_foods_menu?.data);
    yield put(getListFoodsMenuSuccess(listMenu));
    if (typeof onSuccess === 'function') {
      onSuccess({
        menu: listMenu,
        total: res?.list_foods_menu?.metadata.total,
      });
    }
  } catch (error) {
    yield put(getListFoodsMenuFailure(error.message));
  }
}

export function* requestBookingsFoodsSaga(action) {
  const { params, onSuccess } = action;
  try {
    const data = yield call(requestBookingsFoodsApi, params);

    if (data && data.status === 'Succes') {
      yield put(requestBookingsFoodsSuccess(data));
      if (typeof onSuccess === 'function') {
        onSuccess(data);
      }
    } else {
      yield put(requestBookingsFoodsFailure(data));
    }
  } catch (err) {
    yield put(requestBookingsFoodsFailure(err));
  }
}

export default function* orderSaga() {
  yield takeEvery(GET_LIST_FOODS_MENU.HANDLER, getListFoodsMenuSaga);
  yield takeLatest(REQUEST_BOOKINGS_FOODS.HANDLER, requestBookingsFoodsSaga);
}
