export const listVoucherParse = (vouchers) => {
  if (vouchers && vouchers.length > 0) {
    return vouchers.map((voucher) => {
      return {
        ...voucher,
        id: voucher.id,
        imageUrls: voucher.image_urls,
        name: voucher.name,
      };
    });
  }
  return [];
};

export const voucherDetailsParse = (voucher) => {
  if (voucher) {
    return {
      ...voucher,
      id: voucher.id,
      imageUrls: voucher.image_urls,
      name: voucher.name,
    };
  }
  return [];
};
