import {
  GET_LIST_FOODS_MENU,
  REQUEST_BOOKINGS_FOODS,
  ADD_LIST_SELECTED,
  UPDATE_LIST_SELECTED,
  CLEAR_LIST_SELECTED,
} from '../constants/order.constant';

export const getListFoodsMenuHandle = (page, pageSize, filter, onSuccess) => ({
  type: GET_LIST_FOODS_MENU.HANDLER,
  page,
  pageSize,
  filter,
  onSuccess,
});

export const getListFoodsMenuSuccess = (listMenu) => ({
  type: GET_LIST_FOODS_MENU.SUCCESS,
  listMenu,
});

export const getListFoodsMenuFailure = (error) => ({
  type: GET_LIST_FOODS_MENU.FAILURE,
  error,
});

/**
 *  Request bookings foods actions
 */
export const requestBookingsFoodsHandle = (params, onSuccess) => ({
  type: REQUEST_BOOKINGS_FOODS.HANDLER,
  params,
  onSuccess,
});

export const requestBookingsFoodsSuccess = (res) => ({
  type: REQUEST_BOOKINGS_FOODS.SUCCESS,
  res,
});

export const requestBookingsFoodsFailure = (error) => ({
  type: REQUEST_BOOKINGS_FOODS.FAILURE,
  error,
});

/**
 *  Add list selected actions
 */
export const addListSelectedHandle = (params) => ({
  type: ADD_LIST_SELECTED.HANDLER,
  params,
});

/**
 *  Update list selected actions
 */
export const updateListSelectedHandle = (params) => ({
  type: UPDATE_LIST_SELECTED.HANDLER,
  params,
});

export const clearListSelectedHandle = () => ({
  type: CLEAR_LIST_SELECTED.HANDLER,
});
