import { ADD_MODAL } from '../actions/ui.action';

const defaultState = {
  modal: {},
};
const uiReducer = (state = defaultState, action) => {
  const { type, payload } = action;
  switch (type) {
    case ADD_MODAL:
      return {
        ...state,
        modal: payload,
      };

    default:
      return state;
  }
};

export default uiReducer;
