import React from 'react';
import { SwipeableDrawer, IconButton, Hidden } from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import DrawerContainer from '../Drawer/Drawer.container';
import apple_ic from '../../../../assets/icons/apple_ic.png';
import android_ic from '../../../../assets/icons/android_ic.png';
import iconCart from '../../../../assets/icons/icon-cart.png';
import NotificationContainer from '../../../../components/Notification/Notification.container';

import './Contact.scss';

const Contact = (props) => {
  const {
    companyHotline,
    user,
    categories,
    handleDrawerToggle,
    mobileOpen,
  } = props;
  const cartNumber =
    (props.vouchers?.length || 0) +
    props.deliveries?.reduce((total, delivery) => {
      return total + delivery?.data?.length || 0;
    }, 0);
  return (
    <div className="contact_container">
      <div className="container">
        <Hidden smUp>
          <IconButton
            color="inherit"
            aria-label="Open drawer"
            onClick={handleDrawerToggle}
            className="menu_button"
          >
            <MenuIcon className="menu_icon" />
          </IconButton>
          <SwipeableDrawer
            variant="temporary"
            anchor="left"
            open={mobileOpen}
            onOpen={handleDrawerToggle}
            onClose={handleDrawerToggle}
          >
            <DrawerContainer
              user={user}
              categories={categories}
              // onClick={this.handleDrawerToggle}
            />
          </SwipeableDrawer>
        </Hidden>
        <div className="top_container">
          <Hidden smUp>
            <div onClick={props.onNavigateToCart} to="/cart" className="link">
              {cartNumber > 0 ? (
                <div className="cart_badge">
                  <p>{cartNumber}</p>
                </div>
              ) : null}
              <img src={iconCart} className="cart_ic" alt="icon" />
            </div>
            <NotificationContainer
              onClick={props.onNotificationClick}
              notifications={props.listNotifications}
              loadMoreHandle={props.loadMoreHandle}
              totalNotification={props.totalNotification}
            />

            <SwipeableDrawer
              variant="temporary"
              anchor="left"
              open={mobileOpen}
              onOpen={handleDrawerToggle}
              onClose={handleDrawerToggle}
            >
              <DrawerContainer
                user={user}
                categories={categories}
                // onClick={this.handleDrawerToggle}
              />
            </SwipeableDrawer>
          </Hidden>
          <span className="app">
            <Hidden xsDown>
              <p className="hotline">
                Hotline:&nbsp;&nbsp;&nbsp;
                <b>{companyHotline}</b>
              </p>
              <span className="hotline">
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              </span>
            </Hidden>
          </span>
          <span className="app">
            <Hidden xsDown>
              <div className="hotline">
                Ti ng dng &nbsp;<b> MIN PH </b> &nbsp;ti
              </div>
              <div
                style={{
                  display: 'flex',
                  alignItems: 'center',
                  marginTop: '4px',
                  marginLeft: '18px',
                }}
              >
                <a
                  // href={
                  //   companyAppIos ||
                  //   'https://apps.apple.com/vn/app/golden-age-travel/id1490085095'
                  // }
                  target="_blank"
                >
                  <img src={apple_ic} alt="img" />
                </a>
                <a
                  // href={
                  //   companyAppAndroid ||
                  //   'https://play.google.com/store/apps/details?id=com.goldenage.store'
                  // }
                  target="_blank"
                >
                  <img src={android_ic} alt="img" />
                </a>
              </div>
            </Hidden>
          </span>
        </div>
      </div>
    </div>
  );
};

export default Contact;
