/* eslint-disable no-extra-boolean-cast */
import React, { Suspense, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { ErrorBoundary } from 'src/components/ErrorBoundary';
import { getUserInfoHandle, loginSuccess } from 'src/redux/actions/auth.action';
import { getListBannerHandle } from 'src/redux/actions/common.action';
import {
  getAccessTokenSelector,
  getUserInfoSelector,
} from 'src/redux/selectors/auth';
import { currentThemeSelector } from 'src/redux/selectors/theme';
import firebase from 'firebase';
import APIUtils from 'src/utils/apiUtils';
import {
  askForPushNotificationPermission,
  onMessageReceived,
} from 'src/utils/firebaseCloudMessaging';
import { firebaseCloudMessagingConfig } from 'src/constants';
import { sendFCMTokenToReceiveNotifications } from 'src/services/common.service';
import Footer from './footer/Footer.container';
import Header from './header/Header.container';
import { mainRoutes, renderRoutes } from './routes';
import Section from './section/Section';
import { ContainerUI } from './ui';

const firebaseConfig = {
  apiKey: firebaseCloudMessagingConfig.API_KEY,
  projectId: firebaseCloudMessagingConfig.PROJECT_ID,
  messagingSenderId: firebaseCloudMessagingConfig.SENDER_ID,
  appId: firebaseCloudMessagingConfig.APP_ID,
  authDomain: firebaseCloudMessagingConfig.AUTH_DOMAIN,
  storageBucket: firebaseCloudMessagingConfig.STORAGE_BUCKET,
  measurementId: firebaseCloudMessagingConfig.MEASUREMENT_ID,
};

const LayoutApp = () => {
  const currentTheme = useSelector(currentThemeSelector);
  const dispatch = useDispatch();
  const accessToken = useSelector(getAccessTokenSelector);
  const currentUserInfo = useSelector(getUserInfoSelector);
  useEffect(() => {
    dispatch(getListBannerHandle());
    const accessTokenLocal = localStorage.getItem('ACCESS_TOKEN');
    if (accessTokenLocal) {
      dispatch(loginSuccess({ token: accessTokenLocal }));
      APIUtils.setAccessToken(accessTokenLocal);
    }

    if (!firebase.apps.length) {
      firebase.initializeApp(firebaseConfig);
    } else {
      firebase.app(); // if already initialized, use that one
    }
    // firebase.initializeApp(firebaseConfig);
    onMessageReceived();
    if (firebase.auth().currentUser) {
      firebase.auth().signOut();
    }
  }, []);

  useEffect(() => {
    console.log('getUserInfoHandle1111>>>>>');
    dispatch(getUserInfoHandle());
  }, [accessToken]);

  useEffect(() => {
    const LOCAL_FCM_TOKEN = localStorage.getItem('FCM_TOKEN');
    if (currentUserInfo?.id) {
      askForPushNotificationPermission(
        (fcmTokenAfterGrantedPushNotifications) => {
          localStorage.setItem(
            'FCM_TOKEN',
            fcmTokenAfterGrantedPushNotifications
          );
          if (LOCAL_FCM_TOKEN !== fcmTokenAfterGrantedPushNotifications) {
            sendFCMTokenToReceiveNotifications({
              user_id: currentUserInfo.id,
              token: fcmTokenAfterGrantedPushNotifications,
              os: 1,
            });
          }
        }
      );
    }
  }, [currentUserInfo]);

  useEffect(() => {
    document.documentElement.setAttribute('data-theme', currentTheme);
  }, [currentTheme]);

  return (
    <ErrorBoundary>
      <main className="mh-main">
        <div className="main-container">
          <Header />
          <Section>{renderRoutes(mainRoutes)}</Section>
          <Footer />
        </div>
      </main>
      <Suspense fallback={<></>}>
        <ContainerUI />
      </Suspense>
    </ErrorBoundary>
  );
};

export default LayoutApp;
