import React, { useEffect } from 'react';
import { connect, useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import {
  getAccessTokenSelector,
  getUserInfoSelector,
} from 'src/redux/selectors/auth';
import {
  getListNotificationsSelector,
  getTotalNotificationSelector,
} from 'src/redux/selectors/common.selector';
import { logoutHandle } from 'src/redux/actions/auth.action';
import { getListNotificationsHandle } from 'src/redux/actions/common.action';
import APIUtils from 'src/utils/apiUtils';
import {
  getDeliveriesCartSelector,
  getVouchersCartSelector,
} from 'src/redux/selectors/cart.selector';
import { addModal } from 'src/redux/actions/ui.action';
import { MODAL_TYPE } from 'src/constants';
import Header from './Header.view';

const HeaderContainer = (props) => {
  const history = useHistory();
  const dispatch = useDispatch();
  const accessToken = useSelector(getAccessTokenSelector);
  const navigateCreditCard = () => {
    history.push('/user');
  };

  useEffect(() => {
    if (accessToken && props.userInfo?.id) {
      dispatch(
        getListNotificationsHandle(
          1,
          10,
          { is_read: false, user_id: props.userInfo?.id },
          { field: 'id', order: 'desc' }
        )
      );
    }
  }, [accessToken, props.userInfo]);

  const loadMoreHandle = () => {
    const { listNotifications, totalNotification } = props;
    if (listNotifications.length < totalNotification) {
      dispatch(
        getListNotificationsHandle(
          listNotifications.length + 1,
          10,
          { is_read: false, user_id: props.userInfo?.id },
          { field: 'id', order: 'desc' }
        )
      );
    }
  };

  return (
    <Header
      logout={() => {
        APIUtils.setAccessToken('');
        localStorage.clear('ACCESS_TOKEN');
        dispatch(logoutHandle());
      }}
      onNavigateToCart={() => {
        if (accessToken) {
          history.push('/cart');
        } else {
          dispatch(addModal({ type: MODAL_TYPE.FORCE_USER_LOGIN }));
        }
      }}
      onNotificationClick={() => {
        if (accessToken && props.userInfo?.id) {
          dispatch(
            getListNotificationsHandle(
              1,
              10,
              { is_read: false, user_id: props.userInfo?.id },
              { field: 'id', order: 'desc' }
            )
          );
        }
      }}
      deliveries={props.deliveries}
      navigateCreditCard={navigateCreditCard}
      userInfo={props.userInfo}
      accessToken={props.accessToken}
      history={history}
      listNotifications={props.listNotifications}
      loadMoreHandle={loadMoreHandle}
      vouchers={props.vouchers}
      {...props}
    />
  );
};

const mapState = (state) => {
  return {
    accessToken: getAccessTokenSelector(state),
    userInfo: getUserInfoSelector(state),
    listNotifications: getListNotificationsSelector(state),
    totalNotification: getTotalNotificationSelector(state),
    vouchers: getVouchersCartSelector(state),
    deliveries: getDeliveriesCartSelector(state),
  };
};

const mapDispatch = {};

export default connect(mapState, mapDispatch)(HeaderContainer);
