/* eslint-disable no-return-await */
import APIUtils from 'src/utils/apiUtils';
import { API_URL } from 'src/constants';

export const requestJobsRecruitmentApi = async (headers) => {
  return await APIUtils.get(
    `${API_URL}/recuitment/jobs`,
    {},
    {
      'Content-Type': 'application/json',
      ...headers,
    }
  );
};

export const requestJobsRecruitmentInfoApi = async (data, headers) => {
  return await APIUtils.post(`${API_URL}/recuitment`, data, headers);
};
