/* eslint-disable no-return-await */
/* eslint-disable camelcase */
import {
  getDealByIdQuery,
  getListDealQuery,
  getListDealByMerchantQuery,
  getListHappyBucketQuery,
  getListVoucherQuery,
  getListCodeRequiredQuery,
  getListVoucherTypeQuery,
  mark_voucher_from_user,
  getVouchersForListDealQuery,
  use_voucher,
  searchVouchersQuery,
} from 'src/graphql/queries/deal.query';
import { API_URL } from 'src/constants';
import APIUtils from 'src/utils/apiUtils';
import apolloClient from '../graphql/index';

export function getDealByIdApi(id = '') {
  return apolloClient
    .query({
      query: getDealByIdQuery,
      variables: {
        id,
      },
      context: {
        service: 'deal',
      },
    })
    .then((res) => res.data);
}

export function getListDealApi(page, size, filter, sorts = []) {
  return apolloClient
    .query({
      query: getListDealQuery,
      variables: {
        page,
        size,
        filter,
        sorts,
      },
      context: {
        service: 'deal',
      },
    })
    .then((res) => res.data);
}

