// Get List voucher history
export function getListVoucherHistoryApi(page, size) {
  return apolloClient
    .query({
      query: getListVoucherQuery,
      variables: {
        page,
        size,
      },
      fetchPolicy: 'no-cache',
      context: {
        service: 'deal',
      },
    })
    .then((res) => res.data);
}

