/**
 * Delete voucher history
 */

export function deleteVoucherHistoryApi(voucherId) {
  return apolloClient
    .query({
      query: mark_voucher_from_user,
      variables: {
        voucher_id: voucherId,
      },
      context: {
        service: 'deal',
      },
    })
    .then((res) => res.data);
}

export function getVouchersForListDealApi(data) {
  const { deals, promotion_code } = data;
  return apolloClient
    .query({
      query: getVouchersForListDealQuery,
      variables: {
        deals,
        promotion_code,
      },
      context: {
        service: 'deal',
      },
    })
    .then((res) => {
      if (res.errors) {
        throw res.errors;
      }
      return res.data;
    });
}

export const checkPromotionCodeApi = async (data, header) => {
  return await APIUtils.get(
    `${API_URL}/promotion_code/check?code=${data.code}&totalprice=${data.totalPrice}`,
    {
      'Content-Type': 'application/json',
      ...header,
    }
  );
};

/**
 * Use voucher history
 */

export function useVoucherHistoryApi({ voucherId, agent_code }) {
  return apolloClient
    .query({
      query: use_voucher,
      variables: {
        voucher_id: voucherId,
        agent_code,
      },
      context: {
        service: 'deal',
      },
    })
    .then((res) => {
      if (res.errors) {
        throw res.errors;
      }

      return res.data;
    });
}

export function getSearchVouchersApi(search_text, size, page) {
  return apolloClient
    .query({
      query: searchVouchersQuery,
      variables: {
        search_text,
        size,
        page,
      },
      context: {
        service: 'deal',
      },
    })
    .then((res) => res.data);
}
