export function* getListRecruitmentHomeSaga(action) {
  const { onSuccess, page = 1, size = 4 } = action;
  try {
    const res = yield getListNewsApi(page, size, { news_type: 1 });
    const data = listNewsParse(res?.list_news?.data);

    yield put(getListRecruitmentHomeSuccess(data));
    if (typeof onSuccess === 'function') {
      onSuccess(data);
    }
  } catch (error) {
    yield put(getListRecruitmentHomeFailure(error.message));
  }
}

export function* getListRecruitmentSaga(action) {
  const { onSuccess, page = 1, size = 10 } = action;
  try {
    const res = yield getListNewsApi(page, size, { news_type: 1 });
    const data = listNewsParse(res?.list_news?.data);
    yield put(getListRecruitmentSuccess({ ...res?.list_news, data }));
    if (typeof onSuccess === 'function') {
      onSuccess({ ...res?.list_news, data });
    }
  } catch (error) {
    yield put(getListRecruitmentFailure(error.message));
  }
}

export default function* recruitmentSaga() {
  yield takeLatest(
    GET_LIST_RECRUITMENT_HOME.HANDLER,
    getListRecruitmentHomeSaga
  );
  yield takeLatest(GET_LIST_RECRUITMENT.HANDLER, getListRecruitmentSaga);
  // yield takeLatest(GET_HAPPY_BUCKET_BY_ID.HANDLER, getHappyBucketByIdSaga);
}
