export const listBannerParse = (banners) => {
  if (banners && banners.length > 0) {
    return banners.map((banner) => {
      return {
        id: banner.id,
        deepLinkUrl: banner.deep_link_url,
        imageUrl: banner.image_url,
        name: banner.name,
      };
    });
  }
  return [];
};

export const listNotificationsParse = (notifications) => {
  if (notifications && notifications.length > 0) {
    return notifications.map((el) => {
      return {
        id: el.id,
        title: el.title,
        content: el.content,
        is_read: el.is_read,
        is_seen: el.is_seen,
        created_at: el.created_at,
        updated_at: el.updated_at,
      };
    });
  }
  return [];
};

export const notificationDetailParse = (notification) => {
  if (notification) {
    return {
      id: notification.id,
      title: notification.title,
      user_id: notification.user_id,
      content: notification.content,
      is_read: notification.is_read,
      is_seen: notification.is_seen,
      created_at: notification.created_at,
      updated_at: notification.updated_at,
      deep_link_url: notification.deep_link_url,
    };
  }
  return [];
};
