/* eslint-disable import/prefer-default-export */
import gql from 'graphql-tag';

export const getListBranchesQuery = gql`
  query list_branches($page: Int, $size: Int, $filter: BranchFilterInput) {
    list_branches(size: $size, page: $page, filter: $filter) {
      data {
        id
        name
        address
        image_url
        lat
        lng
        merchant {
          image_url
        }
        city {
          name
        }
        phone
        description
      }
      metadata {
        total
      }
    }
  }
`;
