import {
  getDealByIdQuery,
  getListDealByMerchantQuery,
  getListHappyBucketQuery,
} from 'src/graphql/queries/deal.query';
import { getListNewsQuery } from 'src/graphql/queries/news.query';
import apolloClient from '../graphql/index';

export function getDealByIdApi(id = '') {
  return apolloClient
    .query({
      query: getDealByIdQuery,
      variables: {
        id,
      },
      context: {
        service: 'deal',
      },
    })
    .then((res) => res.data);
}

export function getListNewsApi(page, size, filter) {
  return apolloClient
    .query({
      query: getListNewsQuery,
      variables: {
        page,
        size,
        filter,
      },
      context: {
        service: 'news',
      },
    })
    .then((res) => res.data);
}

