import {
  ADD_LIST_SELECTED,
  CLEAR_LIST_SELECTED,
  UPDATE_LIST_SELECTED,
} from '../constants/order.constant';

const initialState = {
  listSelected: [],
};

export default function orderReducer(state = initialState, action) {
  const { type } = action;
  switch (type) {
    case ADD_LIST_SELECTED.HANDLER: {
      const { item } = action.params;

      const newList = state.listSelected.slice();

      newList.push({ ...item, quantity: 1 });

      return {
        ...state,
        listSelected: newList,
      };
    }

    case UPDATE_LIST_SELECTED.HANDLER: {
      const { id, quantity } = action.params;
      const newList = state.listSelected.slice();

      const item = newList.find((el) => el.id === id);
      const index = newList.findIndex((el) => el.id === id);

      if (item !== undefined) {
        if (quantity === 0) {
          newList.splice(index, 1);
        } else {
          item.quantity = quantity;
        }
      }

      return {
        ...state,
        listSelected: newList,
      };
    }
    case CLEAR_LIST_SELECTED.HANDLER: {
      return {
        ...state,
        listSelected: [],
      };
    }
    default:
      return state;
  }
}
