/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
import React, { useState, useRef, useEffect, useCallback } from 'react';
import './DeliveryAddressModal.scss';
// eslint-disable-next-line prettier/prettier
import PlacesAutocomplete, {
  geocodeByAddress,
  getLatLng,
} from 'react-places-autocomplete'; // getLatLng, // geocodeByAddress,
import MaterialIcons from 'react-native-vector-icons/dist/MaterialIcons';
import CalendarCustom from 'src/components/base/Calendar';
import moment from 'moment';

const DATA_TIME = [];

Array(24)
  .fill(0)
  .forEach((obj, i) => {
    DATA_TIME.push(i >= 10 ? `${i}:00` : `0${i}:00`);
    DATA_TIME.push(i >= 10 ? `${i}:15` : `0${i}:15`);
    DATA_TIME.push(i >= 10 ? `${i}:30` : `0${i}:30`);
    DATA_TIME.push(i >= 10 ? `${i}:45` : `0${i}:45`);
  });

const DeliveryAddressModal = (props) => {
  const [branchModalOpen, setBranchModalOpen] = useState(false);
  const branchModalRef = useRef();
  const [dateModalOpen, setDateModalOpen] = useState(false);
  const dateModalRef = useRef();
  const [timeModalOpen, setTimeModalOpen] = useState(false);
  const timeModalRef = useRef();
  const [formInfo, setFormInfo] = useState({});
  const [tempAddress, setTempAddress] = useState('');
  const handleClickOutside = useCallback(
    (event) => {
      if (
        branchModalOpen &&
        branchModalRef &&
        !branchModalRef?.current?.contains(event.target)
      ) {
        setBranchModalOpen(false);
      }
    },
    [branchModalRef, branchModalOpen]
  );
  useEffect(() => {
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [handleClickOutside]);

  return (
    <div className="delivery-address-modal">
      <div className="title_container">
        <div className="title">a ch nhn hng</div>
      </div>
      <div className="underline_title" />
      <div className="row-section">
        <p className="title-item">a im giao *</p>
        <div className="input_container">
          <div style={{ width: '100%' }}>
            {formInfo?.branch?.name && (
              <input
                readOnly
                onClick={() => {
                  setBranchModalOpen(true);
                }}
                value={formInfo?.branch?.name}
                name="start_delivery"
                className="start_delivery"
                placeholder="Chn a im"
              />
            )}
            <input
              readOnly
              onClick={() => {
                setBranchModalOpen(true);
              }}
              value={formInfo?.branch?.address}
              name="start_delivery"
              className="start_delivery"
              placeholder="Chn a im"
            />
          </div>
          <MaterialIcons name="keyboard-arrow-down" color="#3A556A" size={24} />
        </div>
        {branchModalOpen && (
          <div ref={branchModalRef} className="branch_piker_container">
            {props?.branches?.map((branch) => {
              return (
                <p
                  onClick={() => {
                    setFormInfo({ ...formInfo, branch });
                    setBranchModalOpen(false);
                  }}
                  className="branch_address"
                >
                  {branch.address}
                </p>
              );
            })}
          </div>
        )}
      </div>

      <div className="row-section">
        <p className="title-item">a im nhn *</p>

        <PlacesAutocomplete
          value={tempAddress}
          onChange={(address) => {
            setTempAddress(address);
          }}
          onSelect={async (address) => {
            const results = await geocodeByAddress(address);
            const latLng = await getLatLng(results[0]);
            console.log('onSelect', latLng);
            setFormInfo({ ...formInfo, ...latLng, receiveDelivery: address });
            setTempAddress(address);
          }}
        >
          {({
            getInputProps,
            suggestions,
            getSuggestionItemProps,
            loading,
          }) => (
            <div className="input_container">
              <input
                {...getInputProps({
                  placeholder: 'Nhp a im ...',
                  className: 'start_delivery',
                })}
              />
              <div className="autocomplete-dropdown-container">
                {loading && <div>Loading...</div>}
                {suggestions.map((suggestion) => {
                  const className = suggestion.active
                    ? 'suggestion-item-active'
                    : 'suggestion-item';
                  // inline style for demonstration purpose
                  const style = suggestion.active
                    ? { backgroundColor: '#fafafa', cursor: 'pointer' }
                    : { backgroundColor: '#ffffff', cursor: 'pointer' };
                  return (
                    <div
                      {...getSuggestionItemProps(suggestion, {
                        className,
                        style,
                      })}
                    >
                      {suggestion.description}
                    </div>
                  );
                })}
              </div>
            </div>
          )}
        </PlacesAutocomplete>
      </div>

      <div className="row-section">
        <p className="title-item">Ngy giao *</p>
        <div className="input_container">
          <input
            readOnly
            onClick={() => {
              setDateModalOpen(true);
            }}
            value={formInfo?.date}
            name="start_delivery"
            className="start_delivery"
            placeholder="Chn ngy"
          />
          <MaterialIcons name="keyboard-arrow-down" color="#3A556A" size={24} />
        </div>
        {dateModalOpen && (
          <div ref={dateModalRef} className="calendar_custom_container">
            <CalendarCustom
              onChange={(d) => {
                setDateModalOpen(false);
                setFormInfo({
                  ...formInfo,
                  date: moment(d).format('DD/MM/YYYY'),
                });
              }}
              minDate={new Date()}
            />
          </div>
        )}
      </div>

      <div className="row-section">
        <p className="title-item">Gi nhn *</p>
        <div className="input_container">
          <input
            readOnly
            onClick={() => {
              setTimeModalOpen(true);
            }}
            value={formInfo?.time}
            name="start_delivery"
            className="start_delivery"
            placeholder="Chn thi gian"
          />
          <MaterialIcons name="keyboard-arrow-down" color="#3A556A" size={24} />
        </div>
        {timeModalOpen && (
          <div ref={timeModalRef} className="time_picker_container">
            {DATA_TIME.map((time) => {
              return (
                <p
                  onClick={() => {
                    setTimeModalOpen(false);
                    setFormInfo({
                      ...formInfo,
                      time,
                    });
                  }}
                  className="time_text"
                >
                  {time}
                </p>
              );
            })}
          </div>
        )}
      </div>

      <div className="row-section">
        <p className="title-item">Ghi ch</p>
        <div className="input_container">
          <input
            onChange={(evt) => {
              setFormInfo({ ...formInfo, note: evt.target.value });
            }}
            value={formInfo?.note}
            name="start_delivery"
            className="start_delivery"
            placeholder="Ghi ch"
          />
        </div>
      </div>
      <div
        style={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
        }}
      >
        <div
          onClick={() => {
            props.onSubmit(formInfo);
            props.hideModal();
          }}
          className="submit_button"
        >
          Cp nht
        </div>
      </div>
    </div>
  );
};

export default DeliveryAddressModal;
