import React from 'react';
import './HappyBucketItem.scss';

import { Grid } from '@material-ui/core';

const HappyBucketItem = (props) => {
  const { item, onClick } = props;
  return (
    <Grid
      onClick={() => {
        if (typeof onClick === 'function') {
          onClick(item);
        }
      }}
      item
      xs={12}
      sm={12}
      md={6}
      lg={4}
      xl={4}
    >
      <div className="happy_bucket_item_container">
        <div className="image-section">
          {item?.cover_img && (
            <img alt="" className="thumbnail" src={item.cover_img} />
          )}
        </div>
        <div className="content-section">
          <p className="eVoucher-name">{item.name}</p>
          <div className="eVoucher-description">{item.description}</div>
        </div>
      </div>
    </Grid>
  );
};

export default HappyBucketItem;
