/* eslint-disable jsx-a11y/mouse-events-have-key-events */
/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
/* eslint-disable camelcase */
/* eslint-disable no-param-reassign */
/* eslint-disable no-return-assign */
import React from 'react';
import './Notification.scss';
import { NotificationItem } from 'src/components/base';
import bell_ic from '../../assets/icons/bell-ic.png';

class Notification extends React.Component {
  scrollCheck = (event) => {
    const { loadMoreHandle } = this.props;
    const bottom =
      event.target.scrollHeight - Math.round(event.target.scrollTop) ===
      event.target.clientHeight;
    if (bottom) {
      loadMoreHandle();
    }
  };

  render() {
    const { notifications, totalNotification } = this.props;

    return (
      <div className="notification_container">
        {!!totalNotification && (
          <div className="badge">
            <p>{totalNotification}</p>
          </div>
        )}
        <img
          onMouseOver={this.props.onClick}
          className="dropdown_bell_ic"
          src={bell_ic}
          alt="img"
        />

        <div
          className="dropdown_no_notifications"
          id="dropdown_notification"
          onScroll={this.scrollCheck}
        >
          {notifications.length ? (
            notifications.map((el) => <NotificationItem item={el} />)
          ) : (
            <p style={{ fontFamily: 'Google Sans' }}>Bn cha c thng bo</p>
          )}
        </div>
      </div>
    );
  }
}

export default Notification;
