/* eslint-disable no-unused-vars */
/* eslint-disable react/jsx-one-expression-per-line */
import React from 'react';
import './cartItem.scss';
import { CounterInput } from 'src/components/base';
import { convertCurrency } from 'src/utils/currency';
import { Box, Grid } from '@material-ui/core';

function CartItem(props) {
  const { customStyle, deal } = props;
  return (
    <div className={`cart-item-container ${customStyle}`}>
      <Grid container>
        <Grid item xl={4} xs={12} lg={4} sm={12} md={4}>
          <div style={{ height: '156px', position: 'relative' }}>
            <img
              alt=""
              className="image_url"
              src={
                deal?.image_urls &&
                deal?.image_urls.length > 0 &&
                deal?.image_urls[0]
              }
            />
            <div className="content-image-section">
              <img
                alt=""
                className="image_merchant"
                src={deal?.merchant?.image_url}
              />
            </div>
          </div>
        </Grid>
        <Grid
          item
          xl={8}
          lg={8}
          sm={12}
          xs={12}
          md={8}
          style={{
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'space-between',
          }}
        >
          <div className="left-section">
            <div className="left-section-text">
              <p className="dish-name-text">{deal?.name}</p>
              <div style={{}}>
                <p className="sale-price">
                  {convertCurrency(deal?.regular_price)}
                </p>

                {deal?.price &&
                  deal?.regular_discount_type === 0 &&
                  deal?.regular_discount_price && (
                    <div className="current-price-section">
                      <p className="current-price">
                        {convertCurrency(deal?.price)}
                      </p>
                      <div className="percent-item">
                        <p className="percent-value">
                          -{deal?.regular_discount_price}%
                        </p>
                      </div>
                    </div>
                  )}
                {deal?.price &&
                  deal?.regular_discount_type === 1 &&
                  deal?.regular_discount_price && (
                    <div className="current-price-section">
                      <p className="current-price">
                        {convertCurrency(deal.price)}
                      </p>
                      <div className="percent-item">
                        <p className="percent-value">
                          -{deal?.regular_discount_price}K
                        </p>
                      </div>
                    </div>
                  )}
              </div>
            </div>
          </div>

          <div
            style={{
              marginLeft: '20px',
              justifyContent: 'space-between',
              display: 'flex',
              marginTop: '20px',
            }}
          >
            <button
              type="button"
              onClick={() => {
                if (typeof props.onDeleteClick === 'function') {
                  props.onDeleteClick(deal);
                }
              }}
              className="delete-text"
            >
              Xa
            </button>
            <CounterInput
              handleDecrement={() => {
                if (props.deal.quantity > 1) {
                  if (typeof props.onCountChange === 'function') {
                    props.onCountChange(deal, props.deal.quantity - 1);
                  }
                }
              }}
              handleIncrement={() => {
                if (typeof props.onCountChange === 'function') {
                  props.onCountChange(deal, props.deal.quantity + 1);
                }
              }}
              minValue={1}
              value={props.deal.quantity}
            />
          </div>
        </Grid>
      </Grid>
    </div>
  );
}

export default CartItem;
