import React, { useCallback } from 'react';
import classnames from 'classnames';
import { Formik, useFormikContext } from 'formik';
import {
  FormControl,
  FormHelperText,
  FormLabel,
  makeStyles,
} from '@material-ui/core';
import definedWidget from './definedWidget';

import './FormBuilder.scss';

const useStyle = makeStyles({
  root: {
    display: 'flex',
    flexDirection: 'row',
    width: '100%',
  },
});

const FormItem = React.memo(({ data }) => {
  const {
    values,
    errors,
    handleChange,
    setFieldValue,
    setFieldError,
  } = useFormikContext();
  const {
    type = 'input',
    name = '',
    label = '',
    required = false,
    cols = 12,
    props,
  } = data;
  const widgetProps = {
    ...props,
    name,
    value: values[name],
    onChange: handleChange,
    errors,
    values,
    setFieldValue,
    setFieldError,
  };
  const Widget = (
    definedWidget[props.type || type || 'input'] || definedWidget.input
  )(widgetProps);
  const classes = useStyle();
  return (
    <FormControl
      fullWidth={cols === 12}
      classes={classes}
      className={classnames('form-widget', `cols-${cols}`)}
    >
      {label && (
        <FormLabel required={required} htmlFor={name} className="form-label">
          {label}
        </FormLabel>
      )}
      <div className="form-input">
        {Widget}
        {errors[name] && (
          <FormHelperText className="error">{errors[name]}</FormHelperText>
        )}
      </div>
    </FormControl>
  );
});

const FormBuilder = (props) => {
  const {
    configForm = [],
    initialValues = {},
    configValidate = {},
    onSubmit,
    className,
  } = props;

  const buildLayout = useCallback(() => {
    return configForm.map((item) => (
      <FormItem data={item} key={`form.item.${item.key}`} />
    ));
  }, [configForm]);
  return (
    <Formik
      initialValues={initialValues}
      onSubmit={onSubmit}
      validationSchema={configValidate}
    >
      {({ handleSubmit, setFieldValue }) => {
        return (
          <form
            className={classnames('form-builder', className)}
            onSubmit={handleSubmit}
          >
            {buildLayout()}
            <>
              {typeof props.children === 'function'
                ? props.children({ setFieldValue })
                : props.children}
            </>
          </form>
        );
      }}
    </Formik>
  );
};

export default React.memo(FormBuilder);
