/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
import React, { useCallback, useRef, useEffect, useState } from 'react';
import './addressBooking.scss';
import { Button, Grid, withStyles } from '@material-ui/core';
import {
  FolderIcon,
  ClockIcon,
  GroupIcon,
  BookingDateIcon,
  LockIcon,
  LocationIcon,
  CreditCardIcon,
} from 'src/assets/icons';
import { makeStyles } from '@material-ui/core/styles';

import { CounterInput } from 'src/components/base';
import { useDispatch, useSelector } from 'react-redux';
import { getUserInfoSelector } from 'src/redux/selectors/auth';
import moment from 'moment';
import { requestBookingHandle } from 'src/redux/actions/booking.action';
import CalendarCustom from 'src/components/base/Calendar';
import { addModal } from 'src/redux/actions/ui.action';
import { MODAL_TYPE } from 'src/constants';

const DATA_TIME = [];

Array(24)
  .fill(0)
  .forEach((obj, i) => {
    DATA_TIME.push(i >= 10 ? `${i}:00` : `0${i}:00`);
    DATA_TIME.push(i >= 10 ? `${i}:15` : `0${i}:15`);
    DATA_TIME.push(i >= 10 ? `${i}:30` : `0${i}:30`);
    DATA_TIME.push(i >= 10 ? `${i}:45` : `0${i}:45`);
  });

const useStyles = makeStyles(() => ({
  disabledButton: {
    background: '#A6AEBB',
  },
  activeButton: {
    background: '#FE5466',
  },
}));
const StyledButton = withStyles(() => ({
  root: {
    color: '#FFF',
    textTransform: 'unset',
    width: '10rem',
    borderRadius: '5px',
    border: 'none',
    display: 'flex',
    alignSelf: 'center',
    marginTop: '8px',
    margin: '0 auto',
  },
}))(Button);
const ModalAddressBooking = (props) => {
  const classes = useStyles();
  const userInfo = useSelector(getUserInfoSelector);
  const [dateModalOpen, setDateModalOpen] = useState(false);
  const [formInfo, setFormInfo] = useState({
    adultCount: 1,
    childrenCount: 0,
    infantCount: 0,
    branch:
      props?.voucher?.branches && props?.voucher?.branches.length > 0
        ? props?.voucher?.branches[0]
        : {},
    bookingTime:
      moment().minutes() > 30
        ? moment()
            .add('hour', 1)
            .add('minute', 60 - moment().minutes())
            .format('HH:mm')
        : moment()
            .add('hour', 1)
            .add('minute', 30 - moment().minutes())
            .format('HH:mm'),
    bookingDate: moment().add('hour', 1).format('DD/MM/YYYY'),
  });

  const dateModalRef = useRef();
  const [timeModalOpen, setTimeModalOpen] = useState(false);
  const [branchModalOpen, setBranchModalOpen] = useState(false);
  const branchModalRef = useRef();
  const dispatch = useDispatch();
  const timeModalRef = useRef();
  const handleClickOutside = useCallback(
    (event) => {
      if (
        dateModalOpen &&
        dateModalRef &&
        !dateModalRef?.current?.contains(event.target)
      ) {
        setDateModalOpen(false);
      }
      if (
        timeModalRef &&
        timeModalRef &&
        !timeModalRef?.current?.contains(event.target)
      ) {
        setTimeModalOpen(false);
      }
      if (
        branchModalRef &&
        branchModalRef &&
        !branchModalRef?.current?.contains(event.target)
      ) {
        setBranchModalOpen(false);
      }
    },
    [dateModalRef, dateModalOpen]
  );
  useEffect(() => {
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [handleClickOutside]);

  return (
    <div className="modal_address_booking_container">
      <p className="header-title">t ch</p>

      <div className="row-section">
        <GroupIcon />
        <p className="label">S lng ch</p>
      </div>
      <Grid style={{ marginTop: '5px' }} container spacing={3}>
        <Grid
          className="counter-container"
          alignItems="center"
          item
          xl={3}
          lg={4}
          md={4}
          xs={4}
          sm={4}
        >
          <p className="age_title">Ngi ln</p>
          <p className="age_description">Trn 12 tui</p>
          <CounterInput
            minValue={1}
            value={formInfo?.adultCount}
            handleDecrement={() => {
              if (formInfo?.adultCount > 1) {
                setFormInfo({
                  ...formInfo,
                  adultCount: formInfo?.adultCount - 1,
                });
              }
            }}
            handleIncrement={() => {
              setFormInfo({
                ...formInfo,
                adultCount: formInfo?.adultCount + 1,
              });
            }}
          />
        </Grid>
        <Grid
          className="counter-container"
          item
          xl={3}
          lg={4}
          md={4}
          xs={4}
          sm={4}
        >
          <p className="age_title">Tr em</p>
          <p className="age_description">T 6 n 12 tui</p>
          <CounterInput
            value={formInfo?.childrenCount}
            handleDecrement={() => {
              if (formInfo?.childrenCount > 0) {
                setFormInfo({
                  ...formInfo,
                  childrenCount: formInfo?.childrenCount - 1,
                });
              }
            }}
            handleIncrement={() => {
              setFormInfo({
                ...formInfo,
                childrenCount: formInfo?.childrenCount + 1,
              });
            }}
          />
        </Grid>
        <Grid
          className="counter-container"
          item
          xl={3}
          lg={4}
          md={4}
          xs={4}
          sm={4}
        >
          <p className="age_title">Em b</p>
          <p className="age_description">Di 6 tui</p>
          <CounterInput
            value={formInfo?.infantCount}
            handleDecrement={() => {
              if (formInfo?.infantCount > 0) {
                setFormInfo({
                  ...formInfo,
                  infantCount: formInfo?.infantCount - 1,
                });
              }
            }}
            handleIncrement={() => {
              setFormInfo({
                ...formInfo,
                infantCount: formInfo?.infantCount + 1,
              });
            }}
          />
        </Grid>
      </Grid>

      <div style={{ marginTop: '20px' }} className="row-section">
        <BookingDateIcon />
        <p className="label">Ngy t ch</p>
      </div>
      <input
        onClick={() => {
          setDateModalOpen(true);
        }}
        readOnly
        name="table-quantity"
        className="input"
        placeholder="dd/mm/yyyy"
        value={formInfo?.bookingDate}
      />

      <div style={{ marginTop: '20px' }} className="row-section">
        <ClockIcon />
        <p className="label">Thi gian</p>
      </div>
      <input
        readOnly
        onClick={() => {
          setTimeModalOpen(true);
        }}
        value={formInfo?.bookingTime}
        name="booking_time"
        className="input"
        placeholder="Chn thi gian"
      />
      <p className="error">
        {(moment(
          `${formInfo.bookingDate} ${formInfo.bookingTime}`,
          'DD/MM/YYYY HH:mm'
        ).valueOf() -
          moment().valueOf()) /
          3600 <
        60
          ? 'Yu cu thc hin trc 60p'
          : ''}
      </p>
      <div style={{ marginTop: '20px' }} className="row-section">
        <LocationIcon />
        <p className="label">a im</p>
      </div>
      <input
        readOnly
        onClick={() => {
          setBranchModalOpen(true);
        }}
        value={formInfo?.branch?.address}
        name="booking_address"
        className="input"
        placeholder="Chn a im"
      />

      <div style={{ marginTop: '20px' }} className="row-section">
        <FolderIcon />
        <div className="right-content">
          <p className="label inline">Ghi ch</p>
          <p className="inline">(khng bt buc)</p>
        </div>
      </div>
      <input
        value={formInfo.note}
        onChange={(evt) => {
          setFormInfo({ ...formInfo, note: evt.target.value });
        }}
        name="table-quantity"
        className="input"
        placeholder="Nhp ghi ch"
      />
      <div className="row-section between align-center">
        <div className="credit-section">
          <CreditCardIcon />
          <p className="orderer">Ngi t ch: </p>
          <p className="orderer-name">{userInfo.name}</p>
        </div>

        <LockIcon />
      </div>

      <StyledButton
        variant="outlined"
        disabled={
          (moment(
            `${formInfo.bookingDate} ${formInfo.bookingTime}`,
            'DD/MM/YYYY HH:mm'
          ).valueOf() -
            moment().valueOf()) /
            3600 <
          60
        }
        classes={{
          root:
            (moment(
              `${formInfo.bookingDate} ${formInfo.bookingTime}`,
              'DD/MM/YYYY HH:mm'
            ).valueOf() -
              moment().valueOf()) /
              3600 <
            60
              ? classes.disabledButton
              : classes.activeButton,
        }}
        onClick={() => {
          dispatch(
            requestBookingHandle(
              {
                adult_count: formInfo.adultCount || 1,
                children_count: formInfo.childrenCount,
                infant_count: formInfo.infantCount,
                booking_type: 'service',
                check_in_time: moment(
                  `${formInfo.bookingDate} ${formInfo.bookingTime}`,
                  'DD/MM/YYYY HH:mm'
                )
                  .utc()
                  .format(),
                deal_id: props?.voucher?.id,
                branch_id: formInfo?.branch?.id,
                note: formInfo.note,
              },
              () => {
                dispatch(
                  addModal({
                    type: MODAL_TYPE.BOOKING_SUCCESS,
                    data: formInfo?.branch,
                  })
                );
              }
            )
          );
        }}
      >
        Gi yu cu
      </StyledButton>
      {dateModalOpen && (
        <div ref={dateModalRef} className="calendar_custom_container">
          <CalendarCustom
            onChange={(date) => {
              setFormInfo({
                ...formInfo,
                bookingDate: moment(date).format('DD/MM/YYYY'),
              });
              setDateModalOpen(false);
            }}
            minDate={new Date()}
          />
        </div>
      )}
      {timeModalOpen && (
        <div ref={timeModalRef} className="time_picker_container">
          {DATA_TIME.map((time) => {
            return (
              <p
                onClick={() => {
                  setFormInfo({ ...formInfo, bookingTime: time });
                  setTimeModalOpen(false);
                }}
                className="time_text"
              >
                {time}
              </p>
            );
          })}
        </div>
      )}

      {branchModalOpen && (
        <div ref={branchModalRef} className="branch_piker_container">
          {props?.voucher?.branches?.map((branch) => {
            return (
              <p
                onClick={() => {
                  setFormInfo({ ...formInfo, branch });
                  setBranchModalOpen(false);
                }}
                className="branch_address"
              >
                {branch.address}
              </p>
            );
          })}
        </div>
      )}
    </div>
  );
};

export default ModalAddressBooking;
