/* eslint-disable react/jsx-one-expression-per-line */
import React from 'react';
import './chargeCard.scss';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import { convertCurrency } from 'src/utils/currency';

const useStyles = makeStyles(() => {
  return {
    'button-custom': {
      background: '#FF4355',
      width: '100%',
      height: '49px',
      padding: 0,
      display: 'flex',
      alignItems: 'center',
      color: 'white',
    },
  };
});

function ChargeCard(props) {
  const classes = useStyles();

  return (
    <div className="charge-container">
      <div className="price-section">
        <div className="upper">
          <div className="price-upper-content">
            <p className="temporary_money_title">Tm tnh:</p>
            <p className="temporary_money_content">
              {convertCurrency(props.temporaryMoney)}
            </p>
          </div>
        </div>
        {!!props.discountMoney && (
          <div className="upper">
            <div className="price-upper-content">
              <p className="discount_money_title">Gim gi:</p>
              <p className="discount_money_content">
                -{convertCurrency(props.discountMoney)}
              </p>
            </div>
          </div>
        )}
        {!!props.shippingFee && (
          <div className="upper">
            <div className="price-upper-content">
              <p className="discount_money_title">Ph giao hng:</p>
              <p className="discount_money_content">
                {convertCurrency(props.shippingFee)}
              </p>
            </div>
          </div>
        )}
        <div className="line" />
        <div className="price-content">
          <p className="total-price-title">Thnh tin:</p>
          <p className="total-price-text">
            {convertCurrency(props.totalMoney)}
          </p>
        </div>
      </div>
      <div className="button-section">
        <Button
          onClick={props.onClick}
          classes={{
            root: classes['button-custom'],
          }}
          variant="contained"
        >
          <p className="apply-text">{props.title || 'Tin hnh thanh ton'}</p>
        </Button>
      </div>
    </div>
  );
}

export default ChargeCard;
