import gql from 'graphql-tag';

export const getListBannerQuery = gql`
  query {
    list_banners(filter: { category_id_is_null: true }) {
      data {
        id
        image_url
        mobile_image_url
        deep_link_url
        order
      }
      metadata {
        total
      }
    }
  }
`;

export const getSettingByNameQuery = gql`
  query get_setting_by_name($name: String!) {
    get_setting_by_name(name: $name) {
      id
      name
      description
      data
    }
  }
`;

export const getListNotificationsQuery = gql`
  query list_notifications(
    $page: Int
    $size: Int
    $filter: NotificationFilterInput
    $sorts: [SortInput]
  ) {
    list_notifications(
      size: $size
      page: $page
      filter: $filter
      sorts: $sorts
    ) {
      data {
        id
        title
        content
        is_read
        is_seen
        created_at
        updated_at
      }
      metadata {
        total
      }
    }
  }
`;

export const readNotificationQuery = gql`
  query read_notification($id: Int!) {
    read_notification(id: $id) {
      id
      user_id
      content
      title
      deep_link_url
      is_read
      is_seen
      created_at
      updated_at
    }
  }
`;
