import React from 'react';
import './ButtonCustom.scss';

const ButtonCustom = (props) => {
  const { title, icon, customStyle, customTitle, onClick } = props;
  return (
    <button
      type="button"
      className={`button-container ${customStyle}`}
      onClick={onClick}
    >
      {icon ? <span className="icon">{icon}</span> : null}
      <p className={`title-button ${customTitle}`}>{title || ''}</p>
    </button>
  );
};

export default ButtonCustom;
