import {
  GET_LIST_RECRUITMENT,
  POST_RECRUITMENT_INFO,
} from '../constants/fillRecruitment.constant';

export const requestListJobsRecruitmentHandle = (onSuccess) => ({
  type: GET_LIST_RECRUITMENT.HANDLER,
  onSuccess,
});

export const requestListJobsRecruitmentSuccess = (res) => ({
  type: GET_LIST_RECRUITMENT.SUCCESS,
  res,
});

export const requestListJobsRecruitmentFailure = (error) => ({
  type: GET_LIST_RECRUITMENT.FAILURE,
  error,
});

export const requestJobsRecruitmentInfoHandle = (params, onSuccess) => ({
  type: POST_RECRUITMENT_INFO.HANDLER,
  params,
  onSuccess,
});

export const requestJobsRecruitmentInfoSuccess = (res) => ({
  type: POST_RECRUITMENT_INFO.SUCCESS,
  res,
});

export const requestJobsRecruitmentInfoFailure = (error) => ({
  type: POST_RECRUITMENT_INFO.FAILURE,
  error,
});
