import React, { useState } from 'react';
import DefaultAvatar from 'src/assets/icons/default-avatar.svg';

const ImageUpload = (props) => {
  const { title, name, value, setFieldValue } = props;
  const [srcImge, setSrc] = useState(value);

  const onChange = (evt) => {
    evt.preventDefault();
    const file = evt.target.files[0];
    const reader = new FileReader();

    if (file) {
      reader.onloadend = () => {
        setSrc(reader.result);
      };
      reader.readAsDataURL(file);
    }
    setFieldValue(name, file);
  };

  return (
    <div className="image-upload">
      <img className="avatar" alt="upload" src={srcImge || DefaultAvatar} />
      <div className="upload-btn">
        {title || 'Chn nh'}
        <input type="file" className="file-upload" onChange={onChange} />
      </div>
    </div>
  );
};

export default React.memo(ImageUpload);
