import { trackingEvent } from 'src/utils/googleAnalytics';
import { trackEvent } from 'src/utils/facebookPixel';
import { LOGOUT } from '../constants/auth.constant';
import {
  ADD_DELIVERY_TO_CART,
  ADD_VOUCHER_TO_CART,
  UPDATE_LIST_VOUCHER_IN_CART,
} from '../constants/cart.constant';
import { BUY_VOUCHER } from '../constants/voucher.constant';
import { CREATE_DELIVERY_FOOD } from '../constants/delivery.constant';

const initialState = {
  vouchers: [],
  deliveries: [],
};

const handleAddVoucherToCart = (listVoucher, voucher) => {
  trackingEvent({
    category: 'Cart actions',
    action: 'Add to cart',
    label: voucher.name,
    value: voucher.regular_price,
  });
  trackEvent('Add to cart', {
    currency: 'VND',
    value: voucher.regular_price,
    content_category: voucher.category,
    content_name: voucher.name,
    search_string: voucher.search_text,
  });
  const indexVoucher = listVoucher.findIndex((obj) => obj.id === voucher.id);
  const tempListVoucher = listVoucher.slice(0);
  if (indexVoucher >= 0) {
    tempListVoucher[indexVoucher] = {
      ...tempListVoucher[indexVoucher],
      quantity: tempListVoucher[indexVoucher].quantity + voucher.quantity,
    };
  } else {
    tempListVoucher.push(voucher);
  }
  return tempListVoucher;
};

const handleAddDeliveryToCart = (listDelivery, delivery, merchant) => {
  const indexMerchant = listDelivery.findIndex(
    (obj) => obj?.merchant?.id === merchant.id
  );
  const tempListDelivery = listDelivery.slice(0);
  if (indexMerchant >= 0) {
    const indexFood = tempListDelivery[indexMerchant]?.data?.findIndex(
      (obj) => obj?.id === delivery.id
    );
    if (indexFood >= 0) {
      const tempListFood = tempListDelivery[indexMerchant].data.slice(0);
      tempListFood[indexFood] = {
        ...tempListFood[indexFood],
        quantity: tempListFood[indexFood].quantity + delivery.quantity,
      };
      tempListDelivery[indexMerchant] = {
        ...tempListDelivery[indexMerchant],
        data: tempListFood,
      };
    } else {
      tempListDelivery[indexMerchant] = {
        ...tempListDelivery[indexMerchant],
        data: tempListDelivery[indexMerchant].data.concat(delivery),
      };
    }
  } else {
    tempListDelivery.push({ merchant, data: [delivery] });
  }
  return tempListDelivery;
};

export default function cartReducer(state = initialState, action) {
  const { type } = action;
  switch (type) {
    case ADD_VOUCHER_TO_CART.HANDLER:
      return {
        ...state,
        vouchers: handleAddVoucherToCart(state.vouchers, action.voucher),
      };
    case ADD_DELIVERY_TO_CART.HANDLER:
      return {
        ...state,
        deliveries: handleAddDeliveryToCart(
          state.deliveries,
          action.food,
          action.merchant
        ),
      };
    case UPDATE_LIST_VOUCHER_IN_CART.HANDLER:
      return {
        ...state,
        vouchers: action.vouchers,
      };
    case LOGOUT.HANDLER: {
      return {
        ...initialState,
      };
    }
    case BUY_VOUCHER.SUCCESS: {
      return {
        ...state,
        vouchers: [],
      };
    }
    case CREATE_DELIVERY_FOOD.SUCCESS: {
      return {
        ...state,
        deliveries: state.deliveries.filter(
          (obj) => obj?.merchant?.id !== action.deliveryFoodInfo?.merchantId
        ),
      };
    }
    default:
      return state;
  }
}
