import {
  GET_LIST_BRANCH_HOME_BY_MERCHANT_ID,
  GET_LIST_BRANCH_BY_ID,
} from '../constants/branch.constant';

export const getListBranchHomeByMerchantIdHandle = (
  merchantId,
  page,
  size,
  onSuccess
) => ({
  type: GET_LIST_BRANCH_HOME_BY_MERCHANT_ID.HANDLER,
  merchantId,
  page,
  size,
  onSuccess,
});

export const getListBranchHomeByMerchantIdSuccess = (listBranches) => ({
  type: GET_LIST_BRANCH_HOME_BY_MERCHANT_ID.SUCCESS,
  listBranches,
});

export const getListBranchHomeByMerchantIdFailure = (error) => ({
  type: GET_LIST_BRANCH_HOME_BY_MERCHANT_ID.FAILURE,
  error,
});

export const getListBranchByIdHandle = (page, pageSize, filter, onSuccess) => ({
  type: GET_LIST_BRANCH_BY_ID.HANDLER,
  page,
  pageSize,
  filter,
  onSuccess,
});

export const getListBranchByIdSuccess = (listBranches) => ({
  type: GET_LIST_BRANCH_BY_ID.SUCCESS,
  listBranches,
});

export const getListBranchByIdFailure = (error) => ({
  type: GET_LIST_BRANCH_BY_ID.FAILURE,
  error,
});
